:root {
  --color-orange-super-dark: #2a0f00;
  --color-orange-super-light: #f5e3d9;
  --color-red-error-dark-mode: #d46357;
  --color-red-error-light-mode: #c52a1a;
  --color-medium-grey: #464646;
  --color-grey-light-alt: #d6d6d6;
  --color-report-menu-background: #1c1c1c;
  --color-report-builder-border-dark-mode: var(--color-grey-medium);
  --color-report-builder-border-light-mode: var(--color-grey-light);
  --color-report-builder-btn-primary-disabled: var(--color-grey-medium-alt);
  --color-report-builder-btn-primary-disabled-text: var(--color-grey-medium);
  --color-report-builder-btn-small-disabled-border-dark-mode: var(
    --color-grey-medium-alt
  );
  --color-report-builder-btn-small-disabled-border-light-mode: var(
    --color-grey
  );
  --color-report-builder-btn-small-disabled-border: var(
    --color-report-builder-btn-small-disabled-border-dark-mode
  );
  --color-report-builder-btn-small-disabled-text: #464646;
}

.theme-light {
  --tooltip-background: var(--color-white);
}
.theme-light .popoverMenu .popover-body {
  --color-grey-darker: var(--color-white);
  --color-grey-medium: var(--color-grey-light);
  --color-orange-light: var(--color-orange-light-mode);
}
.theme-light .popoverMenu.brightBorder.bs-popover-left > .arrow:before {
  --color-orange-light: var(--color-orange-light-mode);
}
.theme-light .report-builder {
  border-bottom: 1px solid var(--color-report-builder-border-light-mode);
}
.theme-light .report-builder .modal-backdrop {
  background-color: var(--color-white);
}
.theme-light .report-builder .modal-backdrop.show {
  opacity: 0.7;
}
.theme-light .report-builder #report-export,
.theme-light .report-builder #report-success {
  --color-grey-darker-alt: var(--color-white);
  --color-grey-dark: var(--color-grey-light);
  --background-primary: var(--color-grey-lighter);
  --color-grey-medium: var(--color-grey);
  --color-orange-light: var(--color-orange-light-mode);
}
.theme-light .report-builder #report-export input,
.theme-light .report-builder #report-success input {
  --color-grey-dark: var(--color-white);
  --color-grey-alt: var(--color-grey-light);
}
.theme-light .report-builder #report-export .modal-content,
.theme-light .report-builder #report-success .modal-content {
  box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.2);
}
.theme-light .report-landing-page {
  --color-grey-darker-alt: var(--color-grey-lighter);
  --color-grey-darker: var(--color-white);
  --color-grey-medium: var(--color-grey-light);
  --color-grey-dark: var(--color-grey-light);
  --color-grey: var(--color-black-alt);
}
.theme-light .report-landing-page .report-list .report-list-item .report-actions__failed {
  --color-red-error-dark-mode: var(--color-red-error-light-mode);
}
.theme-light .report-landing-page .report-list .report-list-item .overlayedButton,
.theme-light .report-landing-page .report-list .report-list-item .iconButton {
  --color-grey-dark: var(--color-grey-lighter);
  --color-orange-light: var(--color-orange-light-mode);
}
.theme-light .report-landing-page .report-list .report-list-item .report-list-item-menu .report-list-item-menu-item {
  --color-orange-light: var(--color-orange-light-mode);
}
.theme-light .header-action-container {
  --color-orange-light: var(--color-orange-light-mode);
  --color-report-builder-btn-primary-disabled: var(--color-white-alt);
  --color-report-builder-btn-primary-disabled-text: var(--color-grey);
  --color-report-builder-btn-small-disabled-border: var(
    --color-report-builder-btn-small-disabled-border-light-mode
  );
  --color-report-builder-btn-small-disabled-text: #cbcbcb;
}
.theme-light .report-index {
  --color-report-menu-background: var(--color-white);
  --color-grey-medium: var(--color-grey-light);
  --color-disabled: var(--color-medium-grey);
}
.theme-light .report-index .report-nav-section {
  box-shadow: 0 1px 0 0 var(--color-grey-light-alt);
}
.theme-light .report-index .report-nav-section .navigation-line-break {
  border-bottom: 1px solid var(--color-grey);
}
.theme-light .report-view-port {
  --color-black: var(--color-grey-lighter);
  --color-grey-medium: var(--color-grey-light);
  --color-disabled: var(--color-medium-grey);
}
.theme-light .report-view-port .report-filter {
  --color-grey: $color-grey-medium;
}
.theme-light .report-view-port .report-filter .filter-tag-editor .input-field-container .filter-search-icon svg > g > path.outline {
  fill: #464646;
}
.theme-light .report-view-port .report-filter .filter-tag-editor .input-field-container #txt-search-tags-version {
  border: var(--border-size-standard) solid #464646;
}
.theme-light .report-view-port .report-filter .filter-tag-editor .input-field-container #txt-search-tags-version::placeholder {
  color: #464646;
}
.theme-light .report-view-port .report-pages {
  --color-grey-darker-alt: var(--color-white);
  --color-grey-darker: var(--color-white);
  --input-background: var(--color-grey-lighter);
}
.theme-light .report-view-port .report-pages .report-section-controls .title {
  font-weight: bold;
}
.theme-light .report-view-port .report-pages .search-box-label {
  font-weight: var(--font-weight-medium);
}
.theme-light .report-view-port .report-pages .multi-select-container .selected-option,
.theme-light .report-view-port .report-pages .multi-select-container .option-row:hover {
  background-color: var(--color-white);
}
.theme-light .report-view-port .report-pages .page-image-container {
  background-color: #bbbbbb;
}
.theme-light .report-view-port .report-pages .off-switch {
  border-color: var(--color-white);
  background-color: #bbbbbb;
}
.theme-light .report-view-port .report-pages .report-page-hide .page-image,
.theme-light .report-view-port .report-pages .report-page-hide-pined .page-image {
  filter: brightness(70%);
}
.theme-light .report-view-port .report-pages .report-page .title-text,
.theme-light .report-view-port .report-pages .report-page-selected .title-text,
.theme-light .report-view-port .report-pages .report-page-hide .title-text,
.theme-light .report-view-port .report-pages .report-page-pined .title-text,
.theme-light .report-view-port .report-pages .report-page-hide-selected .title-text,
.theme-light .report-view-port .report-pages .report-page-hide-pined .title-text,
.theme-light .report-view-port .report-pages .report-page-dragged .title-text {
  font-weight: bold;
}
.theme-light .report-view-port .report-pages .report-page:hover,
.theme-light .report-view-port .report-pages .report-page-pined:hover,
.theme-light .report-view-port .report-pages .report-page-hide:hover,
.theme-light .report-view-port .report-pages .report-page-hide-pined:hover {
  border-color: #bbbbbb;
}
.theme-light .report-view-port .report-pages .report-page .icon-grey path.fill,
.theme-light .report-view-port .report-pages .report-page .icon-grey path.outline,
.theme-light .report-view-port .report-pages .report-page-pined .icon-grey path.fill,
.theme-light .report-view-port .report-pages .report-page-pined .icon-grey path.outline,
.theme-light .report-view-port .report-pages .report-page-hide .icon-grey path.fill,
.theme-light .report-view-port .report-pages .report-page-hide .icon-grey path.outline,
.theme-light .report-view-port .report-pages .report-page-hide-pined .icon-grey path.fill,
.theme-light .report-view-port .report-pages .report-page-hide-pined .icon-grey path.outline {
  fill: #464646;
}
.theme-light .report-cover,
.theme-light .report-contents {
  --color-black: var(--color-grey-lighter);
  --color-grey-medium: var(--color-grey-light);
  --color-grey-darker: var(--color-white);
  --color-grey-darker-alt: var(--color-grey-lighter);
}
.theme-light .report-cover .report-cover__form .report-cover__form__content,
.theme-light .report-contents .report-cover__form .report-cover__form__content {
  --color-black: var(--color-white);
  --color-grey-dark: var(--color-grey-lighter);
  --color-orange-light: var(--color-orange-light-mode);
}
.theme-light .report-cover .report-cover__form .report-cover__form__content .report-cover__form__field .report-cover__boilerplate-hint-container #report-cover__boilerplate-hint-text,
.theme-light .report-contents .report-cover__form .report-cover__form__content .report-cover__form__field .report-cover__boilerplate-hint-container #report-cover__boilerplate-hint-text {
  color: #464646;
}
.theme-light .report-cover .report-cover__form .report-cover__form__content .report-cover__form__field .report-cover__calendar-container .react-date-picker.react-date-picker--closed.react-date-picker--enabled .react-date-picker__calendar.react-date-picker__calendar--open .react-calendar.report-cover__calendar,
.theme-light .report-cover .report-cover__form .report-cover__form__content .report-cover__form__field .report-cover__calendar-container .react-date-picker.react-date-picker--closed.react-date-picker--enabled .react-date-picker__calendar.react-date-picker__calendar--closed .react-calendar.report-cover__calendar,
.theme-light .report-cover .report-cover__form .report-cover__form__content .report-cover__form__field .report-cover__calendar-container .react-date-picker.react-date-picker--open.react-date-picker--enabled .react-date-picker__calendar.react-date-picker__calendar--open .react-calendar.report-cover__calendar,
.theme-light .report-cover .report-cover__form .report-cover__form__content .report-cover__form__field .report-cover__calendar-container .react-date-picker.react-date-picker--open.react-date-picker--enabled .react-date-picker__calendar.react-date-picker__calendar--closed .react-calendar.report-cover__calendar,
.theme-light .report-contents .report-cover__form .report-cover__form__content .report-cover__form__field .report-cover__calendar-container .react-date-picker.react-date-picker--closed.react-date-picker--enabled .react-date-picker__calendar.react-date-picker__calendar--open .react-calendar.report-cover__calendar,
.theme-light .report-contents .report-cover__form .report-cover__form__content .report-cover__form__field .report-cover__calendar-container .react-date-picker.react-date-picker--closed.react-date-picker--enabled .react-date-picker__calendar.react-date-picker__calendar--closed .react-calendar.report-cover__calendar,
.theme-light .report-contents .report-cover__form .report-cover__form__content .report-cover__form__field .report-cover__calendar-container .react-date-picker.react-date-picker--open.react-date-picker--enabled .react-date-picker__calendar.react-date-picker__calendar--open .react-calendar.report-cover__calendar,
.theme-light .report-contents .report-cover__form .report-cover__form__content .report-cover__form__field .report-cover__calendar-container .react-date-picker.react-date-picker--open.react-date-picker--enabled .react-date-picker__calendar.react-date-picker__calendar--closed .react-calendar.report-cover__calendar {
  --background-primary: var(--color-grey-lighter);
  --color-grey: #464646;
}
.theme-light .report-cover .d3-section-misc-details__edit-button,
.theme-light .report-contents .d3-section-misc-details__edit-button {
  --color-grey-dark: var(--color-grey-light);
}
.theme-light .report-cover .form-item:empty,
.theme-light .report-contents .form-item:empty {
  --color-black: var(--color-white);
}
.theme-light .report-cover .preview-panel,
.theme-light .report-contents .preview-panel {
  color: var(--color-white);
}
.theme-light .report-builder-iconHeadlineNote .icon-container svg > g path.outline {
  fill: var(--color-red-error-light);
}

.report-builder {
  display: flex;
  flex-flow: row;
  width: 100%;
  height: 100%;
  font-family: var(--font-family);
  line-height: var(--lineheight-regular);
  border-bottom: 1px solid var(--color-report-builder-border-dark-mode);
}
.report-builder.loading {
  border-bottom: none;
}
.report-builder.responsive {
  border-bottom: none;
}
.report-builder button:focus {
  outline: 1px auto -webkit-focus-ring-color;
}
.report-builder button:hover {
  outline: none;
}
.report-builder .font-strike {
  text-decoration: line-through;
  color: var(--color-disabled);
}
.report-builder .toggle-container {
  justify-content: flex-end;
  margin-left: auto;
}
.report-builder .ui-tooltip-tip {
  z-index: 1000;
}
