.report-list-item-menu-item {
  display: flex;
  margin-bottom: 1rem;
}
.report-list-item-menu-item:last-of-type {
  margin-bottom: 0rem;
}
.report-list-item-menu-item .link-button {
  background: none;
  border: 0;
  color: var(--color-orange-light);
  padding: 0;
  text-decoration: none;
  display: flex;
}
.report-list-item-menu-item .link-button svg {
  display: flex;
}
.report-list-item-menu-item .link-button > *:not(:last-child) {
  margin-right: 1rem;
}
.report-list-item-menu-item .link-button:hover {
  text-decoration: underline;
  color: var(--color-orange-light);
}
.report-list-item-menu-item .link-button:active, .report-list-item-menu-item .link-button:focus {
  color: var(--color-orange-light);
}
.report-list-item-menu-item .link-button:active:focus {
  color: var(--color-orange-light);
  background: none;
}
