.radio-group {
  display: flex;
  flex-direction: column;
  width: max-content;
}
.radio-group.direction-row {
  flex-direction: row;
}
@media screen and (max-width: 991px) {
  .radio-group.direction-row {
    flex-direction: column;
  }
}
.radio-group.direction-row .radio-label {
  margin-bottom: 0;
  margin-right: var(--s8);
  padding-bottom: 0;
}
.radio-group .radio-label {
  align-items: center;
  display: flex;
  flex-direction: row;
  padding: var(--s2) 0;
}
.radio-group .radio-label [type=radio] {
  margin: 0;
}

.radio-group-label {
  color: var(--input-color);
  font-size: var(--font-size-regular);
  font-weight: var(--font-weight-medium);
  padding: var(--s2) 0;
}

.radio-button {
  align-items: center;
  appearance: none;
  display: flex;
  justify-content: center;
  position: relative;
}
.radio-button::before {
  background: var(--input-background);
  border: var(--input-radio-border-size) solid var(--input-radio-border);
  border-radius: 50%;
  content: "";
  cursor: pointer;
  display: inline-block;
  flex: 0 0 var(--input-radio-size);
  height: var(--input-radio-size);
  transition: all 0.2s ease-in-out;
  width: var(--input-radio-size);
}
.radio-button::before:focus {
  outline: none;
}
.radio-button::after {
  background-color: var(--color-white);
  border-radius: 50%;
  content: "";
  display: none;
  flex: 0 0 var(--font-size-xxs);
  height: var(--font-size-xxs);
  position: absolute;
  width: var(--font-size-xxs);
}
.radio-button ~ .radio-label-text {
  color: var(--project-color-main);
  cursor: pointer;
  font-size: var(--font-size-regular);
  padding-left: var(--s3);
}
.radio-button:checked::before {
  background: var(--background-secondary);
  border-color: var(--background-secondary);
}
.radio-button:checked::after {
  display: inline-block;
}
.radio-button:checked:disabled::before {
  background: var(--color-red-lighter);
}
.radio-button:disabled::before {
  border-color: var(--color-grey-light);
  cursor: not-allowed;
}
.radio-button:disabled::before ~ .radio-label-text {
  color: var(--color-grey);
  cursor: not-allowed;
}
