.inlinetext-container {
  display: inline-flex;
  flex-direction: column;
  margin-bottom: var(--s6);
  max-width: 100%;
}
.inlinetext-container .textarea-container {
  align-items: center;
  border: var(--border-size-standard) solid transparent;
  border-radius: var(--border-radius);
  display: inline-flex;
  flex-direction: row;
  max-width: 100%;
  position: relative;
}
.inlinetext-container .textarea-container .text-area {
  scrollbar-color: var(--color-grey-variant) var(--scrollbar-track-color);
  scrollbar-width: thin;
  /* stylelint-disable-next-line scss/selector-no-redundant-nesting-selector */
  background: transparent;
  border: var(--border-size-standard) solid transparent;
  border-radius: var(--border-radius);
  box-sizing: border-box;
  color: var(--input-color);
  font-family: var(--font-default);
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-bold);
  line-height: var(--lineheight-xl);
  margin-top: var(--sn2px);
  padding-left: var(--s6px);
  resize: none;
  top: var(--s6);
}
.inlinetext-container .textarea-container .text-area::-webkit-scrollbar {
  border-radius: var(--s2);
  height: var(--s2);
  width: var(--s2);
}
.inlinetext-container .textarea-container .text-area::-webkit-scrollbar-track {
  background: var(--color-grey);
}
.inlinetext-container .textarea-container .text-area::-webkit-scrollbar-thumb {
  background: var(--color-grey);
  border-radius: var(--s2);
}
.inlinetext-container .textarea-container .text-area::-webkit-scrollbar-thumb:hover {
  background: var(--color-grey-dark);
}
.inlinetext-container .textarea-container .text-area {
  -ms-scrollbar-3dlight-color: var(--color-grey);
  -ms-scrollbar-face-color: var(--color-grey);
  scrollbar-face-color: var(--color-grey);
  -ms-scrollbar-track-color: var(--color-grey);
  scrollbar-track-color: var(--color-grey);
}
@media screen and (max-width: 767px) {
  .inlinetext-container .textarea-container .text-area {
    font-size: var(--font-size-regular);
    line-height: var(--lineheight-regular);
  }
}
.inlinetext-container .textarea-container .text-area::placeholder {
  color: var(--input-color-placeholder);
}
.inlinetext-container .textarea-container .text-area.focus-visible {
  box-shadow: none;
}
.inlinetext-container .textarea-container .text-area:focus {
  border-color: transparent;
  border-width: var(--border-size-standard);
  outline: none;
}
.inlinetext-container .textarea-container:hover {
  background: var(--input-background);
}
.inlinetext-container .textarea-container.error {
  border-color: var(--color-error);
}
.inlinetext-container .textarea-container.label-lifted .edit-icon {
  opacity: 0;
}
.inlinetext-container .textarea-container .edit-icon {
  padding: 0 var(--s6px);
  position: absolute;
}
.inlinetext-container .character-count {
  align-items: center;
  color: var(--label-color);
  display: flex;
  flex-direction: row;
  font-size: var(--font-size-s);
  margin-top: var(--s3);
  padding-left: var(--s3);
}
.inlinetext-container .character-count.error {
  color: var(--color-error);
}
.inlinetext-container .character-count .info,
.inlinetext-container .character-count .error {
  margin-right: var(--s2);
}
.inlinetext-container .inline-text-error {
  align-items: flex-start;
  color: var(--color-error);
  display: flex;
  font-size: var(--font-size-s);
  line-height: var(--lineheight-s);
  margin-top: var(--s2);
  padding-left: var(--s3);
}
.inlinetext-container .inline-text-error .input-error-icon {
  flex: 0 0 var(--s4);
  margin-right: var(--s2);
  margin-top: var(--s2px);
  width: var(--s4);
}
