.report-list-item-menu {
  list-style: none;
  padding: 0;
  background-color: var(--color-grey-darker);
  font-family: var(--font-family);
  font-size: 1rem;
  font-weight: 500;
  margin: 0;
}
.report-list-item-menu li {
  display: flex;
  margin-bottom: 1rem;
  align-items: center;
}
.report-list-item-menu li > *:not(:last-child) {
  margin-right: 1rem;
}
.report-list-item-menu li > *:last-child {
  margin-bottom: 0rem;
}
.report-list-item-menu li svg {
  height: 1.5rem;
  width: 1.5rem;
}

.menu-not-shareable {
  display: flex;
  padding: 0;
  margin: var(--s5) 0 0 0;
}
.menu-not-shareable .menu-alert-icon {
  display: flex;
  padding: 0.2rem 0;
  margin: 0 calc(var(--s1) + var(--s2px)) 0 0;
}
.menu-not-shareable .menu-alert-icon svg {
  height: 0.875rem;
}
.menu-not-shareable .menu-note {
  display: flex;
  font-size: calc(var(--s4) - var(--s2px));
  font-family: inherit;
  font-weight: 500;
  color: var(--input-color);
  line-height: var(--lineheight-s);
}
