.report-section-controls {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  justify-content: space-between;
  align-items: center;
}
.report-section-controls .title {
  width: 70%;
}
.report-section-controls .icon-button {
  border: none;
  background: none;
}
.report-section-controls .up-down-controls {
  text-align: center;
  margin-left: auto;
  display: flex;
}
.report-section-controls .up-down-controls svg {
  width: var(--s6);
  height: var(--s6);
  padding: var(--s3px);
}
.report-section-controls .up-down-controls > *:not(:first-child) {
  margin-left: calc(var(--s2) - var(--s2px));
}
.report-section-controls .up-down-controls .icon-button {
  padding: 0;
}
.report-section-controls .up-down-controls .icon-orange {
  cursor: pointer;
}
.report-section-controls .default-switch {
  opacity: 0.5;
}
.report-section-controls .default-switch:hover {
  opacity: initial;
}
