@charset "UTF-8";
.select-component {
  display: flex;
  flex-direction: column;
  position: relative;
}
.select-component.basic {
  background: none;
  border: 0;
  flex-direction: column;
}
.select-component.basic label {
  color: var(--input-color);
  font-size: var(--font-size-regular);
  font-weight: var(--font-weight-medium);
  padding: var(--s2) 0;
  position: relative;
}
.select-component.basic label .helper-text {
  font-size: var(--font-size-s);
  font-weight: var(--font-weight-regular);
  margin-top: var(--s2);
}
.select-component.basic label .helper-text .helper-icon {
  margin-right: var(--s2);
}
.select-component .select-label {
  display: inline-block;
  font-weight: var(--font-weight-bold);
  padding: var(--s4) 0 0 0;
  pointer-events: none;
  transition: all var(--transition);
}
.select-component .select-container .error .text-input:focus {
  border-color: var(--color-error);
}
.select-component .select-container .select-element {
  align-items: center;
  display: flex;
}
.select-component .select-container .select-element select {
  appearance: none;
  background-color: var(--input-background);
  border: var(--border-standard);
  border-color: var(--input-border);
  border-radius: var(--border-radius, 0);
  color: var(--input-color);
  font-size: var(--font-size-regular);
  height: var(--s10);
  margin-right: 0;
  max-width: 100%;
  padding-left: var(--padding-half);
  padding-right: var(--s8);
  position: relative;
  width: max-content;
}
.select-component .select-container .select-element select:focus {
  border-color: var(--input-border-active);
  outline: none;
}
.select-component .select-container .select-element::after {
  content: "";
  display: inline-block;
  font-size: var(--font-size-large);
  pointer-events: none;
  position: relative;
  right: var(--s5);
  transform: rotate(90deg);
}
