.filter-tag-editor {
  max-width: 100%;
}
.filter-tag-editor textarea:focus,
.filter-tag-editor input:focus {
  outline: none;
}
.filter-tag-editor .title {
  font-weight: var(--font-weight-medium);
  margin-bottom: var(--margin-standard);
  margin-bottom: var(--s1);
}
.filter-tag-editor .description {
  color: var(--input-color);
  font-size: var(--font-size-s);
}
.filter-tag-editor .instructions {
  color: var(--input-color);
  font-size: var(--font-size-s);
  padding-top: 0.25rem;
}
.filter-tag-editor .icon-yellow,
.filter-tag-editor .icon-white {
  margin: 0 var(--s2) 0 0;
}
.filter-tag-editor .input-field-container {
  min-width: 350px;
  border-width: var(--s1px);
  background: var(--input-background);
  color: var(--project-color-main);
  display: flex;
  overflow: hidden;
  position: relative;
  overflow: visible;
  /* Width */
  /* Track */
  /* Handle */
  /* Handle on hover */
}
.filter-tag-editor .input-field-container.with-selected-tags {
  margin-bottom: calc(var(--s3) + var(--s1px));
}
.filter-tag-editor .input-field-container .multi-select-container {
  top: 49px;
}
.filter-tag-editor .input-field-container .multi-select-container > .scrollable {
  max-height: 350px;
}
.filter-tag-editor .input-field-container.focus {
  border-color: 2px solid var(--color-white);
  outline: none;
}
.filter-tag-editor .input-field-container .filter-search-icon {
  position: absolute;
  left: var(--s3);
  height: 100%;
}
.filter-tag-editor .input-field-container .filter-search-icon svg > g > path.outline {
  fill: var(--color-grey);
}
.filter-tag-editor .input-field-container .input-field {
  background: var(--input-background);
  color: inherit;
  border: var(--border-size-standard) solid var(--color-grey);
  border-radius: var(--border-radius);
  font-size: var(--font-size-regular);
  font-weight: var(--font-weight-regular);
  line-height: var(--input-lineheight);
  min-height: var(--s12);
  padding: var(--s3);
  padding-left: var(--s10);
  width: 100%;
}
.filter-tag-editor .input-field-container .input-field::placeholder {
  color: var(--color-grey);
}
.filter-tag-editor .input-field-container .input-field:-webkit-autofill {
  caret-color: var(--input-color);
  margin-bottom: 0;
  margin-top: 0;
  padding-bottom: 0;
}
.filter-tag-editor .input-field-container .input-field:focus {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  border-color: var(--color-orange);
}
.filter-tag-editor .input-field-container.focus .text-input {
  border: var(--border-size-standard) solid var(--text-alternate-lighter);
}
.filter-tag-editor .input-field-container ::placeholder {
  color: var(--color-grey);
  opacity: 1;
}
.filter-tag-editor .input-field-container ::-webkit-input-placeholder {
  color: var(--color-grey);
  opacity: 1;
}
.filter-tag-editor .input-field-container ::-ms-input-placeholder {
  color: var(--color-grey);
  opacity: 1;
}
.filter-tag-editor .input-field-container ::-webkit-scrollbar {
  width: 6px;
}
.filter-tag-editor .input-field-container ::-webkit-scrollbar-track {
  background: var(--input-background);
}
.filter-tag-editor .input-field-container ::-webkit-scrollbar-thumb {
  background: var(--color-grey-medium);
  border-radius: 10px !important;
}
.filter-tag-editor .input-field-container ::-webkit-scrollbar-thumb:hover {
  background: var(--color-grey-dark);
}
.filter-tag-editor .input-field-container .has-icon-left {
  bottom: var(--s9);
  height: var(--s10);
  left: var(--s3);
  pointer-events: none;
  position: absolute;
  width: var(--s10);
  z-index: 4;
}
.filter-tag-editor .input-field-container .has-icon-left + .text-input {
  padding-left: var(--s10);
}
.filter-tag-editor .a-form-error {
  color: var(--color-red);
  line-height: 1.5;
  background-color: transparent;
  display: block;
}
.filter-tag-editor .pill-container {
  align-items: center;
}
@media screen and (max-width: 767px) {
  .filter-tag-editor .pill-container > * {
    margin-bottom: var(--margin-quarter);
  }
}
@media screen and (min-width: 767px) {
  .filter-tag-editor .pill-container[data-tags-right=true] {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    line-height: var(--lineheight-s);
  }
}
.filter-tag-editor .pill-container .version-tag-container .clearable-display-pill-report-filter {
  background: var(--color-yellow);
}
.filter-tag-editor .pill-container .version-tag-container .clearable-display-pill-report-filter span {
  color: var(--color-black-alt);
}
.filter-tag-editor .pill-container .tag-container .clearable-display-pill-report-filter {
  background: var(--color-orange);
}
.filter-tag-editor .pill-container .tag-container .clearable-display-pill-report-filter span {
  color: var(--color-white);
}
.filter-tag-editor .pill-container .clearable-display-pill-report-filter button:active, .filter-tag-editor .pill-container .clearable-display-pill-report-filter button:focus, .filter-tag-editor .pill-container .clearable-display-pill-report-filter button:hover {
  background-color: transparent;
}
