:root {
  --complexity-bar-indicator-background: linear-gradient(to right, #027996, #2A9C87 50%, #0C7DFC);
  --complexity-bar-height: 5px;
  --bullet-full: 13px;
  --bullet-half: 7.5px;
  --complexity-label-text: var(--color-grey);
}

.theme-light {
  --complexity-label-text: var(--color-grey-medium);
}

.numbers, .complexity-range {
  color: var(--complexity-label-text);
  font-size: var(--font-size-s);
}

.complexity-scale {
  margin-bottom: var(--s6);
  margin-top: var(--s17);
}
.complexity-scale.show-range {
  margin-top: var(--s10);
}

.complexity-range {
  display: flex;
  height: var(--s5);
  justify-content: space-between;
  margin-bottom: var(--s2);
}

.complexity-bar {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}

.complexity-tips {
  height: var(--s8);
  position: absolute;
  width: 100%;
}
.complexity-tips .ui-tooltip {
  position: absolute;
}

.complexity-container {
  background: var(--complexity-bar-track-background);
  border-radius: calc(var(--complexity-bar-height) / 2);
  height: var(--complexity-bar-height);
  overflow: hidden;
  position: relative;
  width: 100%;
}
.complexity-container .complexity-indicator {
  background: var(--complexity-bar-indicator-background);
  border-radius: calc(var(--complexity-bar-height) / 2);
  height: var(--complexity-bar-height);
  position: absolute;
  width: 100%;
}

.complexity-bullet-hollow {
  background: var(--background-color);
  height: calc((var(--bullet-half) * 2) * 1.25);
  pointer-events: none;
  position: absolute;
  width: 100%;
  z-index: 3;
}

.complexity-bullet {
  background: var(--complexity-bar-indicator-background);
  height: calc(var(--bullet-full) * 2);
  position: absolute;
  width: 100%;
  z-index: 2;
}

.slices {
  align-items: center;
  display: flex;
  justify-content: space-evenly;
  position: absolute;
  width: 100%;
  z-index: 1;
}
.slices div {
  height: var(--complexity-bar-height);
  width: 2px;
}

.numbers {
  display: flex;
  flex-direction: row;
  margin-top: var(--s5);
  width: 100%;
}
.numbers span {
  flex: 1;
  text-align: center;
}
