.app-banner {
  position: fixed;
  z-index: 10;
}
.app-banner .app-banner-content {
  background: var(--color-yellow);
  color: var(--color-black);
  font-size: var(--font-size-s);
  padding: var(--s3);
  position: fixed;
  text-align: center;
  width: 100vw;
}
@media screen and (min-width: 992px) {
  .app-banner .app-banner-content {
    padding: var(--s3) var(--s23);
  }
}
.app-banner div,
.app-banner p {
  line-height: var(--s6);
  margin: 0;
}

.app-layout .app-banner .app-banner-content a {
  color: var(--color-black);
  text-decoration: underline;
}
.app-layout .app-banner .app-banner-content a:hover {
  color: var(--color-black);
}
