a.menu-item,
button.menu-item {
  align-items: center;
  background: var(--menuitem-background);
  border-color: transparent;
  border-style: solid;
  border-width: var(--s2px);
  color: var(--menuitem-color);
  display: flex;
  height: var(--s16);
  justify-content: center;
  margin: var(--s2px) 0 var(--s2) 0;
  padding: 0 var(--s4);
  position: relative;
  text-decoration: none;
  transition: all var(--transition);
}
@media screen and (min-width: 768px) {
  a.menu-item,
button.menu-item {
    margin: 0;
    padding: 0;
  }
  a.menu-item .menu-item-icon,
button.menu-item .menu-item-icon {
    text-align: center;
    width: var(--s21);
  }
  a.menu-item .menu-item-title,
a.menu-item .menu-item-arrow,
button.menu-item .menu-item-title,
button.menu-item .menu-item-arrow {
    display: none;
  }
}
a.menu-item .menu-item-icon > svg.outline path.outline,
button.menu-item .menu-item-icon > svg.outline path.outline {
  fill: var(--menuitem-color);
}
@media screen and (max-width: 767px) {
  a.menu-item .menu-item-icon > svg.outline path.outline,
button.menu-item .menu-item-icon > svg.outline path.outline {
    color: var(--project-color-secondary);
  }
}
a.menu-item .menu-item-icon > svg path.fill,
button.menu-item .menu-item-icon > svg path.fill {
  fill: transparent;
}
a.menu-item .menu-item-state-icon,
button.menu-item .menu-item-state-icon {
  align-items: center;
  background-color: var(--color-grey-light);
  border-radius: var(--s2);
  display: none;
  height: calc(var(--s4) + var(--s2px));
  justify-content: center;
  margin-left: var(--s4);
  padding-left: var(--s1px);
  width: calc(var(--s4) + var(--s2px));
}
@media screen and (max-width: 767px) {
  a.menu-item .menu-item-state-icon,
button.menu-item .menu-item-state-icon {
    display: flex;
    flex-shrink: 0;
  }
}
a.menu-item .menu-item-state-icon .state-icon,
button.menu-item .menu-item-state-icon .state-icon {
  height: var(--s3);
  width: var(--s3);
}
a.menu-item:hover,
button.menu-item:hover {
  color: var(--menuitem-color-active);
}
@media screen and (min-width: 768px) {
  a.menu-item:hover,
button.menu-item:hover {
    background: var(--menuitem-background-active);
    border-color: var(--navbar-color);
  }
}
a.menu-item:hover:not(.no-label) .menu-label,
button.menu-item:hover:not(.no-label) .menu-label {
  color: var(--menuitem-color);
  left: calc(100% - 1px);
  opacity: 1;
  visibility: visible;
}
a.menu-item.add-icon > .menu-item-icon > svg path.outline,
button.menu-item.add-icon > .menu-item-icon > svg path.outline {
  fill: transparent;
}
@media screen and (max-width: 1199px) {
  a.menu-item.menu-item-footer,
button.menu-item.menu-item-footer {
    flex: 1;
  }
}
a.menu-item.menu-item-footer .menu-item-arrow,
button.menu-item.menu-item-footer .menu-item-arrow {
  display: none;
}
a.menu-item .menu-item-icon,
button.menu-item .menu-item-icon {
  display: flex;
  width: var(--s12);
}
a.menu-item .menu-item-title,
button.menu-item .menu-item-title {
  color: var(--menu-link-color);
  font-size: var(--font-size-regular);
  line-height: var(--font-size-regular);
  overflow: hidden;
  text-decoration: none;
  text-overflow: ellipsis;
  white-space: nowrap;
}
@media screen and (max-width: 767px) {
  a.menu-item .menu-item-title,
button.menu-item .menu-item-title {
    color: var(--project-color-secondary);
  }
}
a.menu-item .menu-item-arrow,
button.menu-item .menu-item-arrow {
  flex: 1 0;
  margin-left: var(--s4);
  text-align: end;
}
a.menu-item:hover, a.menu-item.menu-active,
button.menu-item:hover,
button.menu-item.menu-active {
  color: var(--menuitem-color-active);
}
a.menu-item:hover .menu-item-icon svg:not(.fill) path.outline, a.menu-item.menu-active .menu-item-icon svg:not(.fill) path.outline,
button.menu-item:hover .menu-item-icon svg:not(.fill) path.outline,
button.menu-item.menu-active .menu-item-icon svg:not(.fill) path.outline {
  fill: transparent;
}
a.menu-item:hover .menu-item-icon svg:not(.fill) path.fill, a.menu-item.menu-active .menu-item-icon svg:not(.fill) path.fill,
button.menu-item:hover .menu-item-icon svg:not(.fill) path.fill,
button.menu-item.menu-active .menu-item-icon svg:not(.fill) path.fill {
  fill: var(--menuitem-color-label);
}
a.menu-item:hover .menu-item-title, a.menu-item.menu-active .menu-item-title,
button.menu-item:hover .menu-item-title,
button.menu-item.menu-active .menu-item-title {
  text-decoration: underline;
}
@media screen and (min-width: 768px) {
  a.menu-item.menu-active,
button.menu-item.menu-active {
    background: var(--menuitem-background-active);
  }
}
a.menu-item.menu-active .menu-item-title,
button.menu-item.menu-active .menu-item-title {
  color: var(--menuitem-color-label);
  text-decoration: none;
}

.menu-separator {
  background: var(--menuitem-separator);
  height: var(--s3px);
}

.menu-item .menu-label {
  display: none;
}

@media screen and (min-width: 768px) {
  .menu-item.no-label .menu-label {
    display: none;
  }

  .menu-item:not(.no-label) .menu-label {
    background: var(--menuitem-background);
    border: var(--popup-border);
    border-radius: var(--border-radius);
    box-shadow: var(--popup-shadow);
    box-sizing: border-box;
    cursor: pointer;
    display: block;
    font-size: var(--font-size-s);
    height: 2.375rem;
    left: calc(100% - 400px);
    line-height: var(--s6);
    padding: 0.375rem var(--s3) var(--s2) var(--s3);
    position: absolute;
    visibility: hidden;
    white-space: nowrap;
  }
  .menu-item:not(.no-label) .menu-label::before, .menu-item:not(.no-label) .menu-label::after {
    border: 0.5625rem solid transparent;
    border-top-color: transparent;
    content: "";
    left: 0;
    position: absolute;
  }
  .menu-item:not(.no-label) .menu-label::before {
    border-right-color: var(--menuitem-background);
    transform: translate(-1.0625rem, var(--s3px));
    z-index: 1;
  }
  .menu-item:not(.no-label) .menu-label::after {
    border-right-color: var(--popup-border-color);
    margin-left: -0.125rem;
    transform: translate(-1rem, var(--s3px));
  }
}
@media screen and (min-width: 1600px) {
  a.menu-item,
button.menu-item {
    display: flex;
    justify-content: center;
  }
  a.menu-item .menu-item-icon,
button.menu-item .menu-item-icon {
    margin-left: 0.75rem;
    width: var(--s15);
  }
  a.menu-item > .menu-item-title,
button.menu-item > .menu-item-title {
    display: inline-block;
  }
  a.menu-item:hover:not(.no-label) .menu-label,
button.menu-item:hover:not(.no-label) .menu-label {
    left: calc(100% - 400px);
    visibility: hidden;
  }
  a.menu-item:hover .menu-item-title,
button.menu-item:hover .menu-item-title {
    color: var(--menuitem-color-active);
  }
}
