.report-filters {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 40px;
  margin: var(--s6) var(--s4) var(--s6) 0;
}
.report-filters .report-filter-label {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0 0 var(--s3) 0;
}
.report-filters .report-filter-label .search-box-label {
  margin: 0 var(--s2);
}
.report-filters .report-filter-label .report-filter-hint {
  background: none;
  padding: 0;
  margin: 0;
  border: none;
  display: flex;
}
.report-filters .report-filter-label .report-filter-hint:focus {
  outline: 1px solid var(--text-primary);
}
.report-filters .report-filter-label svg[aria-label^=information] {
  cursor: pointer;
}
