.notification {
  display: flex;
  flex: 0 1 50%;
  flex-direction: column;
  align-items: flex-start;
}
@media screen and (min-width: 577px) {
  .notification {
    flex-basis: 25%;
  }
}
.notification .notification-label {
  font-size: var(--font-size-s);
  line-height: var(--s4);
  color: var(--color-grey);
  text-decoration: underline;
}
.notification .number-dot-container {
  color: var(--text-primary);
  display: flex;
  flex-direction: row;
}
.notification .circle {
  height: 7px;
  width: 7px;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  margin: auto var(--s2) auto 0;
}
.notification .circle.green {
  background-color: var(--color-green);
}
.notification .circle.grey {
  background-color: var(--color-grey-medium);
}
