.report-builder-iconHeadlineNote {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  padding: var(--s20) var(--s25) var(--s25) var(--s25);
  justify-content: center;
  font-family: var(--font-family);
  background-color: var(--color-page-background);
}
.report-builder-iconHeadlineNote label {
  margin: 0;
}
@media (max-width: 768px) {
  .report-builder-iconHeadlineNote {
    padding: var(--s20) 0 var(--s25) 0;
  }
}
.report-builder-iconHeadlineNote .loader-contain .loader-holder .loader {
  margin: 0 0 var(--s7) 0;
}
.report-builder-iconHeadlineNote .icon-container {
  display: flex;
  justify-content: center;
  margin: 0 0 var(--s6) 0;
}
.report-builder-iconHeadlineNote .title {
  display: flex;
  justify-content: center;
  text-align: center;
  font-size: var(--font-size-xxl);
  font-weight: var(--font-weight-bold);
  line-height: var(--lineheight-xxl);
  color: var(--project-color-main);
  letter-spacing: 0;
  margin: 0 0 var(--s6) 0;
}
.report-builder-iconHeadlineNote .note {
  display: flex;
  justify-content: center;
  text-align: center;
  color: var(--project-color-main);
  line-height: var(--lineheight-l);
  font-size: var(--font-size-l);
  font-weight: var(--font-weight-regular);
  letter-spacing: 0;
}
.report-builder-iconHeadlineNote .note.note--with-links {
  display: block;
  padding: 0 20%;
}
.report-builder-iconHeadlineNote .note.note--with-links .note__link {
  color: var(--color-orange-theme);
  text-decoration: underline;
}
.report-builder-iconHeadlineNote .actions {
  display: flex;
  justify-content: center;
  margin: var(--s6) 0 0 0;
}
