.toggle-container {
  color: var(--text-primary);
}
.toggle-container *,
.toggle-container *::before,
.toggle-container *::after {
  box-sizing: unset;
}
.toggle-container .toggle-checkbox {
  align-items: center;
  cursor: pointer;
  display: inline-flex;
}
.toggle-container .toggle-checkbox .toggle-background {
  background: var(--toggle-background);
  border: var(--s2px) solid var(--toggle-border);
  border-radius: var(--s4);
  display: inline-block;
  height: var(--s6);
  justify-content: center;
  position: relative;
  transition: all var(--transition);
  width: calc(var(--s10) + var(--s2px));
}
.toggle-container .toggle-checkbox .toggle-background::before {
  background: var(--toggle-thumb-background);
  border-radius: 50%;
  box-sizing: content-box;
  content: " ";
  display: block;
  height: var(--s4);
  left: var(--s1);
  margin-top: var(--s1);
  position: absolute;
  transition: all var(--transition);
  width: var(--s4);
}
.toggle-container .toggle-checkbox .toggle-background.disabled {
  background-color: var(--toggle-background-disabled);
  border: var(--s2px) solid transparent;
}
.toggle-container .toggle-checkbox .toggle-background.disabled::before {
  background-color: var(--toggle-thumb-background-disabled);
}
.toggle-container .toggle-label {
  margin-left: var(--s3);
}
.toggle-container input {
  appearance: none;
  display: none;
}
.toggle-container input:checked + label .toggle-background {
  background: var(--toggle-background-checked);
  border: var(--s2px) solid transparent;
}
.toggle-container input:checked + label .toggle-background::before {
  background: var(--toggle-thumb-background-checked);
  box-sizing: unset;
  height: var(--s5);
  left: var(--s5);
  margin-top: var(--s2px);
  width: var(--s5);
}
.toggle-container input:checked + label .toggle-background.disabled {
  background-color: var(--toggle-background-disabled-checked);
}
.toggle-container input:checked + label .toggle-background.disabled::before {
  background-color: var(--toggle-thumb-background-disabled-checked);
}
