.header-action-container {
  color: var(--color-white);
  padding: var(--s4);
  display: flex;
  justify-content: flex-start;
  padding-left: 40px;
  border-bottom: 1px solid var(--color-grey-medium);
}
.header-action-container label {
  color: var(--project-color-main);
  height: var(--s6);
  font-size: var(--s4);
  font-weight: var(--font-weight-medium);
  letter-spacing: 0;
  line-height: var(--s6);
  text-align: right;
}
.header-action-container .link-report-builder {
  color: var(--color-orange-light);
  display: flex;
  flex-direction: row;
  align-items: center;
  background: none;
  border: none;
  margin: 0;
  padding: 0;
  cursor: pointer;
}
.header-action-container .link-report-builder svg {
  margin: var(--s2);
}
.header-action-container .link-report-builder div {
  color: inherit;
}
.header-action-container .actions {
  padding-left: var(--s4);
  margin-left: auto;
}
.header-action-container .actions button {
  margin-left: var(--s4);
  font-size: var(--font-size-s);
  padding: var(--s1) var(--s3);
}
.header-action-container .actions button:focus {
  outline: 1px solid var(--text-primary);
}
.header-action-container .actions #btn-report-preview {
  color: var(--color-orange-light);
  border-color: var(--color-orange-light);
}
.header-action-container .actions #btn-report-preview:focus:active {
  background: none;
}
.header-action-container .actions #btn-report-preview:disabled {
  background-color: var(--color-report-builder-btn-primary-disabled);
  border: var(--s1px) solid var(--color-report-builder-btn-primary-disabled-text);
  color: var(--color-report-builder-btn-primary-disabled-text);
}
.header-action-container .actions .button-small-custom-disabled {
  background-color: transparent;
  border: var(--s1px) solid var(--color-report-builder-btn-small-disabled-border);
  color: var(--color-report-builder-btn-small-disabled-text);
  pointer-events: none;
  text-decoration: none;
}
.header-action-container .modal-backdrop.show {
  opacity: 0.7;
}

.last-updated {
  width: 60%;
  font-size: var(--font-size-xs);
}
.last-updated span {
  color: var(--color-red-light);
}
