.report-list .report-list__sub-headline {
  flex: 1 1 auto;
  color: inherit;
  font-family: var(--font-family);
  font-size: calc(var(--s8) - var(--s2px));
  font-weight: var(--font-weight-bold);
  letter-spacing: 0;
  line-height: var(--lineheight-l);
  margin: 0 0 var(--s6) 0;
}
.report-list .report-list__pending-reports .report-list-item,
.report-list .report-list__generated-reports .report-list-item {
  margin-bottom: var(--s3);
}
.report-list .report-list__pending-reports .report-list-item:last-of-type,
.report-list .report-list__generated-reports .report-list-item:last-of-type {
  margin-bottom: 0;
}
.report-list .report-list__header {
  display: grid;
  grid-template-columns: [report-name] 55% [report-date] 20% [show-archive-switch] auto;
  margin: 0 var(--s6) var(--s6) var(--s6);
  align-items: center;
  font-size: var(--font-size-regular);
  font-family: inherit;
  font-weight: var(--font-weight-regular);
  letter-spacing: 0;
  line-height: var(--lineheight-regular);
  color: var(--color-grey);
}
.report-list .report-list__header span {
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}
@media (max-width: 767px) {
  .report-list .report-list__header {
    display: flex;
    grid-template-columns: none;
    margin: 0 0 calc(var(--s4) + var(--s2px)) 0;
  }
}
.report-list .report-list__header .switch-container {
  margin-left: unset;
}
.report-list .report-list__header .switch-container .on-switch,
.report-list .report-list__header .switch-container .off-switch {
  margin-top: 0;
}
.report-list .report-list__header .switch-container .switch-label {
  margin: 0 0 0 var(--s2);
}
