.report-cover {
  width: 100%;
  color: var(--project-color-main);
  display: flex;
  flex-flow: column;
  height: auto;
  background-color: var(--color-page-background);
}
.report-cover button[aria-label]:focus {
  outline: 1px auto -webkit-focus-ring-color;
}
.report-cover .report-cover__container {
  height: auto;
