.menu-switch {
  background: transparent;
  border: 0;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  height: 1.125rem;
  justify-content: space-around;
  margin: 0 var(--s4);
  padding: 0;
  position: relative;
  top: 0;
  width: 1.125rem;
  z-index: 10;
}
@media screen and (min-width: 768px) {
  .menu-switch {
    display: none;
  }
}
.menu-switch:hover {
  background: none;
}
.menu-switch span {
  background: var(--menuitem-color);
  border-radius: 0;
  height: 2px;
  position: relative;
  transform-origin: 1px;
  transition: all 0.1s linear;
  width: 1.125rem;
}
.menu-switch :first-child {
  transform: rotate(0);
}
.menu-switch :nth-child(2) {
  opacity: 1;
  transform: translateX(0);
}
.menu-switch :nth-child(3) {
  transform: rotate(0);
}

.menu-switch.true span {
  background: var(--color-orange-theme);
  height: 3px;
  transform-origin: 2px;
  width: 22px;
}
.menu-switch.true :first-child {
  transform: rotate(43deg);
}
.menu-switch.true :nth-child(2) {
  opacity: 0;
  transform: scale(0);
}
.menu-switch.true :nth-child(3) {
  transform: rotate(-42deg);
}
