.header-account {
  align-items: center;
  color: var(--text-primary);
  display: flex;
  justify-content: space-between;
  width: var(--s20);
}

.header-account-initials {
  align-items: center;
  background-color: var(--header-account-initials);
  border-radius: calc(var(--s9) / 2);
  display: flex;
  height: var(--s9);
  justify-content: center;
  width: var(--s9);
}
.header-account-initials .header-account-initials-text {
  color: var(--color-grey-dark);
  font-size: var(--font-size-s);
  font-weight: var(--font-weight-medium);
  text-transform: uppercase;
}
