.theme-dark .powerpoint-group text {
  fill: #FFFFFF;
}
.theme-dark .powerpoint-group text tspan {
  fill: #FFFFFF;
}
.theme-dark .powerpoint-group path,
.theme-dark .powerpoint-group rect {
  opacity: 0.5;
  stroke: #7D7D7D;
}
.theme-dark .powerpoint-group path[fill="#FFFFFF" i],
.theme-dark .powerpoint-group rect[fill="#FFFFFF" i] {
  fill: #1E1E1E;
  opacity: 1;
  stroke: none;
}
.theme-dark .powerpoint-group path[fill^=url],
.theme-dark .powerpoint-group rect[fill^=url] {
  opacity: 1;
}
