.ui-tooltip {
  display: inline-block;
  position: relative;
}
.ui-tooltip .ui-tooltip-tip {
  background: var(--tooltip-background);
  border: var(--s1px) solid var(--tooltip-border-color);
  border-radius: var(--s2px);
  color: var(--text-primary);
  display: inline-block;
  font-size: var(--font-size-s);
  line-height: var(--s06);
  padding: calc(var(--s2) + var(--s2px)) var(--s3);
  position: absolute;
  text-align: center;
  white-space: pre-line;
  width: max-content;
  z-index: 1;
}
.ui-tooltip .ui-tooltip-tip::before, .ui-tooltip .ui-tooltip-tip::after {
  border-style: solid;
  border-width: var(--s2);
  content: " ";
  position: absolute;
}
.ui-tooltip.ui-tooltip-unthemed {
  --tooltip-background: var(--color-white);
}
.ui-tooltip.ui-tooltip-unthemed .ui-tooltip-tip {
  background: var(--color-white);
  color: var(--color-black);
}
.ui-tooltip.ui-tooltip-top .ui-tooltip-tip {
  bottom: calc(100% + var(--s3));
  left: 50%;
  transform: translateX(-50%);
}
.ui-tooltip.ui-tooltip-top .ui-tooltip-tip::before, .ui-tooltip.ui-tooltip-top .ui-tooltip-tip::after {
  border-color: var(--tooltip-background) transparent transparent transparent;
  left: 50%;
  margin-left: var(--sn2);
  top: 100%;
}
.ui-tooltip.ui-tooltip-top .ui-tooltip-tip::before {
  border-color: var(--tooltip-border-color) transparent transparent transparent;
}
.ui-tooltip.ui-tooltip-right .ui-tooltip-tip {
  left: calc(100% + var(--s3));
  top: 50%;
  transform: translateY(-50%);
}
.ui-tooltip.ui-tooltip-right .ui-tooltip-tip::before, .ui-tooltip.ui-tooltip-right .ui-tooltip-tip::after {
  border-color: transparent var(--tooltip-background) transparent transparent;
  margin-top: var(--sn2);
  right: 100%;
  top: 50%;
}
.ui-tooltip.ui-tooltip-right .ui-tooltip-tip::before {
  border-color: transparent var(--tooltip-border-color) transparent transparent;
  right: calc(100% + var(--s1px));
}
.ui-tooltip.ui-tooltip-bottom .ui-tooltip-tip {
  left: 50%;
  top: calc(100% + var(--s3));
  transform: translateX(-50%);
}
.ui-tooltip.ui-tooltip-bottom .ui-tooltip-tip::before, .ui-tooltip.ui-tooltip-bottom .ui-tooltip-tip::after {
  border-color: transparent transparent var(--tooltip-background) transparent;
  bottom: 100%;
  left: 50%;
  margin-left: var(--sn2);
}
.ui-tooltip.ui-tooltip-bottom .ui-tooltip-tip::before {
  border-color: transparent transparent var(--tooltip-border-color) transparent;
  bottom: calc(100% + var(--s1px));
}
.ui-tooltip.ui-tooltip-left .ui-tooltip-tip {
  right: calc(100% + var(--s3));
  top: 25%;
  transform: translateY(-19%);
}
.ui-tooltip.ui-tooltip-left .ui-tooltip-tip::before, .ui-tooltip.ui-tooltip-left .ui-tooltip-tip::after {
  border-color: transparent transparent transparent var(--tooltip-background);
  left: 100%;
  margin-top: var(--sn2);
  top: 25%;
}
.ui-tooltip.ui-tooltip-left .ui-tooltip-tip::before {
  border-color: transparent transparent transparent var(--tooltip-border-color);
  left: calc(100% + var(--s1px));
}
