.ui-checkbox {
  align-items: center;
  color: var(--text-primary);
  display: flex;
  padding: var(--s2) 0;
}
.ui-checkbox [type=checkbox] {
  margin: 0;
}
.ui-checkbox .checkbox {
  align-items: center;
  appearance: none;
  display: flex;
  justify-content: center;
  position: relative;
}
.ui-checkbox .checkbox::before {
  background: var(--input-background);
  border: var(--input-radio-border-size) solid var(--input-radio-border);
  box-sizing: border-box;
  content: "";
  cursor: pointer;
  display: inline-block;
  height: var(--input-radio-size);
  transition: all 0.2s ease-in-out;
  width: var(--input-radio-size);
}
.ui-checkbox .checkbox::before:focus {
  outline: none;
}
.ui-checkbox .checkbox::after {
  border: var(--s2px) solid var(--color-white);
  border-width: 0 var(--s2px) var(--s2px) 0;
  content: "";
  display: none;
  height: rem(12);
  position: absolute;
  transform: rotate(45deg) scale(1) translate(-1px, -2px);
  width: rem(6);
}
.ui-checkbox .checkbox ~ .checkbox-label-text {
  color: var(--text-primary);
  cursor: pointer;
  font-size: var(--font-size-regular);
  padding-left: var(--padding-standard);
}
.ui-checkbox .checkbox:checked::before {
  background: var(--background-secondary);
  border-color: var(--background-secondary);
}
.ui-checkbox .checkbox:checked::after {
  display: inline-block;
}
.ui-checkbox .checkbox:checked:disabled::before {
  background: var(--color-red-lighter);
}
.ui-checkbox .checkbox:disabled::before {
  border-color: var(--color-grey-light);
  cursor: not-allowed;
}
.ui-checkbox .checkbox:disabled::before ~ .checkbox-label-text {
  color: var(--color-grey);
  cursor: not-allowed;
}
