.notifications-list .header-row {
  padding: var(--s6);
  display: grid;
  grid-template-columns: [check] auto [emptyspace] auto [sort] 21%;
  align-items: center;
}
@media (max-width: 768px) {
  .notifications-list .header-row {
    display: flex;
    justify-content: space-between;
    padding: var(--s6) var(--s6) var(--s6) 1.125rem;
  }
}
.notifications-list .header-row label {
  margin-bottom: 0;
}
.notifications-list .header-row .selection-deletion {
  grid-column: 1/2;
  display: flex;
}
.notifications-list .header-row .selection-deletion .ui-checkbox {
  height: calc(var(--s8) - var(--s2px));
  margin-right: var(--s6);
}
.notifications-list .header-row .selection-deletion .ui-checkbox .checkbox-label-text {
  padding-left: 0;
}
.notifications-list .header-row .selection-deletion .selection-deletion__delete-button {
  padding: calc(var(--s1) + var(--s2px)) var(--s3);
  background-color: transparent;
  border: 1px solid var(--color-orange-theme);
  color: var(--color-orange-theme);
  font-size: var(--font-size-s);
  line-height: var(--lineheight-xs);
  font-family: var(--font-family);
  font-weight: var(--font-weight-medium);
}
.notifications-list .header-row .selection-deletion .selection-deletion__delete-button:hover {
  text-decoration: underline;
}
.notifications-list .header-row .selection-deletion .selection-deletion__delete-button:focus:active {
  background: var(--button-primary-color-active);
  border-color: var(--button-primary-color-active);
  outline: none;
}
.notifications-list .header-row .column-label {
  grid-column: 3/4;
  display: flex;
  align-items: center;
}
.notifications-list .header-row .column-label .column-label-text {
  display: flex;
  margin-right: var(--s3);
  color: var(--project-color-secondary);
  font-weight: var(--font-weight-medium);
  font-size: var(--font-size-regular);
  letter-spacing: 0;
  line-height: var(--s6);
}
@media (max-width: 768px) {
  .notifications-list .header-row .column-label {
    display: none;
  }
}
.notifications-list .header-row .button-link {
  display: none;
}
@media (max-width: 768px) {
  .notifications-list .header-row .button-link {
    display: flex;
  }
}
.notifications-list .header-row.no-notifications {
  visibility: hidden;
}
.notifications-list .header-row.no-notifications .selection-deletion {
  display: none;
}
.notifications-list .notification-item-wrapper {
  margin: 0 0 var(--s3) 0;
}
.notifications-list .notification-item-wrapper:last-of-type {
  margin: 0;
}
