.clearable-display-pill-report-filter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--color-grey-light);
  color: var(--background-default);
  padding: var(--s1) var(--s2);
  border-radius: var(--s3);
  margin-right: var(--margin-half);
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-medium);
  line-height: var(--lineheight-xs);
  letter-spacing: 0;
  max-width: 90vw;
}
.clearable-display-pill-report-filter > *:not(:first-child) {
  margin-left: var(--s2);
}
@media screen and (min-width: 992px) {
  .clearable-display-pill-report-filter {
    max-width: 100%;
  }
}
.clearable-display-pill-report-filter span {
  display: inline-block;
}
.clearable-display-pill-report-filter button {
  border: 0;
  background-color: transparent;
  line-height: var(--lineheight-xs);
  padding: 0;
  width: var(--s3);
}
.clearable-display-pill-report-filter button svg {
  height: calc(var(--s3) - var(--s3px));
  width: calc(var(--s3) - var(--s3px));
  vertical-align: unset;
}
