.switch, .off-switch, .on-switch {
  width: var(--s11);
  height: var(--s6);
  border-radius: var(--s3);
  cursor: pointer;
  position: relative;
}

.circle, .off-switch div, .on-switch div {
  width: calc(var(--s4) + var(--s2px));
  height: calc(var(--s4) + var(--s2px));
  border-radius: 50%;
}

.on-switch {
  background-color: var(--button-primary-color);
  border: 1px solid transparent;
}
.on-switch div {
  background-color: var(--color-white);
  position: absolute;
  right: var(--s3px);
  top: var(--s2px);
  bottom: var(--s2px);
}

.off-switch {
  background-color: var(--color-grey-dark);
  border: 1px solid #707070;
}
.off-switch div {
  background-color: var(--color-white);
  width: var(--s4);
  height: var(--s4);
  position: absolute;
  left: var(--s1);
  top: var(--s3px);
  bottom: var(--s2px);
}

.switch-container {
  display: flex;
  flex-flow: row;
  align-items: center;
  justify-self: flex-end;
  margin-left: calc(var(--s5) - var(--s2px));
  border: none;
  background: none;
}
.switch-container .switch-label {
  color: var(--project-color-main);
  font-family: var(--font-family);
  line-height: var(--lineheight-regular);
  margin: 0 0 0 var(--s2);
  font-size: var(--font-size-regular);
}
