.banner {
  background-color: var(--color-blue);
  display: flex;
  height: var(--s16);
  justify-content: space-between;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .banner {
    height: var(--s14);
  }
}
.banner .banner-content {
  align-items: center;
  display: flex;
  margin-right: var(--sn6);
}
@media screen and (max-width: 767px) {
  .banner .banner-content {
    margin-right: var(--sn4);
  }
}
.banner .banner-text {
  color: var(--color-white);
  font-weight: var(--font-weight-medium);
  margin-left: var(--s4);
  margin-right: var(--s4);
}
.banner .banner-close-button {
  display: flex;
  margin-right: var(--s12);
}
@media screen and (max-width: 767px) {
  .banner .banner-close-button {
    margin-right: var(--s4);
  }
}

.banner.success {
  background-color: var(--color-green);
}

.banner.error {
  background-color: var(--color-red-error-light);
}

.banner.warning {
  background-color: var(--color-gold);
}
