.navigation {
  background-color: var(--navbar-color);
  border-right: var(--s1px) solid var(--border-primary);
  display: flex;
  flex-direction: column;
  min-height: calc(100vh - var(--s16));
  width: var(--s21);
}
@media screen and (max-width: 767px) {
  .navigation {
    background-color: var(--navbar-color-mobile);
    height: calc(100vh - var(--s16));
    left: 0;
    position: absolute;
    top: var(--s16);
    transform: translateX(-110%);
    transition: transform 0.2s ease-in-out;
    width: 100vw;
  }
  .navigation.true {
    transform: translateX(0);
    z-index: 1;
  }
  .navigation.true .add-popover {
    display: none;
  }
  .navigation .menu-navigation {
    background-color: var(--navbar-color-mobile);
    padding: var(--s4) var(--s4) 0;
  }
  .navigation .menu-footer {
    background-color: var(--navbar-color-mobile);
    padding: 0 var(--s4) var(--s4);
  }
}
@media screen and (max-width: 767px) and (min-width: 768px) {
  .navigation .menu-footer {
    margin-top: auto;
  }
}
@media screen and (max-width: 767px) {
  .navigation .menu-footer .notification-bell-menu {
    background: transparent;
  }
}
@media screen and (max-width: 767px) {
  .navigation .menu-footer .notification-badge {
    margin-left: var(--sn3);
  }
}
@media screen and (max-width: 767px) {
  .navigation .link-icon {
    padding-bottom: var(--s1);
  }
}
@media screen and (min-width: 1600px) {
  .navigation {
    width: var(--navbar-width-xxl);
  }
  .navigation .menu-item {
    justify-content: flex-start;
  }
}
@media screen and (min-width: 768px) {
  .navigation .menu-footer {
    margin-top: auto;
  }
}
@media screen and (max-width: 767px) {
  .navigation.true ~ .page-content {
    position: fixed;
  }
}
