.notification-list-item, .unread-notification-list-item {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  border-radius: var(--s2px);
  background-color: var(--background-primary);
  box-shadow: 0 var(--sn1px) 0 0 var(--table-shadow-color);
  border: 1px solid transparent;
  padding: var(--content-spacing);
  margin: 0 0 var(--s3) 0;
  font-size: var(--font-size-regular);
}
.notification-list-item:last-of-type, .unread-notification-list-item:last-of-type {
  margin: 0;
}
@media (max-width: 768px) {
  .notification-list-item, .unread-notification-list-item {
    align-items: unset;
    padding: 1.125rem;
  }
}
.notification-list-item .notification-content, .unread-notification-list-item .notification-content {
  display: grid;
  grid-template-columns: [details] 80% [date] auto;
  align-items: flex-start;
  width: 100%;
}
@media (max-width: 768px) {
  .notification-list-item .notification-content, .unread-notification-list-item .notification-content {
    display: flex;
  }
}
.notification-list-item .notification-content .notification-details, .unread-notification-list-item .notification-content .notification-details {
  grid-column: 1/2;
  display: flex;
  align-items: flex-start;
  width: 100%;
  color: var(--project-color-main);
  line-height: var(--lineheight-regular);
  font-weight: var(--font-weight-bold);
}
@media (max-width: 768px) {
  .notification-list-item .notification-content .notification-details, .unread-notification-list-item .notification-content .notification-details {
    display: grid;
    grid-template-columns: [single-col] auto;
    grid-template-rows: [top-row] auto [bottom-row] auto;
  }
}
.notification-list-item .notification-content .notification-details__top-row, .unread-notification-list-item .notification-content .notification-details__top-row {
  grid-row: 1/2;
  display: flex;
}
.notification-list-item .notification-content .notification-selection, .unread-notification-list-item .notification-content .notification-selection {
  display: flex;
  padding-top: var(--s1);
  margin-right: 1.125rem;
}
.notification-list-item .notification-content .notification-selection .ui-checkbox .checkbox-label-text, .unread-notification-list-item .notification-content .notification-selection .ui-checkbox .checkbox-label-text {
  padding-left: 0;
}
@media (max-width: 768px) {
  .notification-list-item .notification-content .notification-selection, .unread-notification-list-item .notification-content .notification-selection {
    padding-top: 0;
    grid-row: 1/2;
  }
}
.notification-list-item .notification-content .notification-sender-title, .unread-notification-list-item .notification-content .notification-sender-title {
  color: var(--project-color-main);
  display: flex;
  line-height: 1.5rem;
  font-weight: bold;
  align-items: flex-start;
}
.notification-list-item .notification-content .notification-intials, .unread-notification-list-item .notification-content .notification-intials {
  display: flex;
  margin: 0 1.125rem 0 0;
}
@media (max-width: 768px) {
  .notification-list-item .notification-content .notification-intials, .unread-notification-list-item .notification-content .notification-intials {
    margin: 0 var(--s3) 0 0;
    padding: var(--s1) 0 0 0;
    grid-row: 1/2;
  }
}
.notification-list-item .notification-content .notification-sender, .unread-notification-list-item .notification-content .notification-sender {
  display: flex;
  padding: var(--s3px) var(--s3px);
  white-space: nowrap;
}
@media (max-width: 768px) {
  .notification-list-item .notification-content .notification-sender, .unread-notification-list-item .notification-content .notification-sender {
    white-space: pre-wrap;
    padding: 0px;
    grid-row: 1/2;
  }
}
.notification-list-item .notification-content .notification-dash, .unread-notification-list-item .notification-content .notification-dash {
  display: flex;
  padding: var(--s3px) var(--s3px);
}
@media (max-width: 768px) {
  .notification-list-item .notification-content .notification-dash, .unread-notification-list-item .notification-content .notification-dash {
    display: none;
  }
}
.notification-list-item .notification-content .notification-title, .unread-notification-list-item .notification-content .notification-title {
  padding: var(--s3px) var(--s3px);
}
.notification-list-item .notification-content .notification-title a, .unread-notification-list-item .notification-content .notification-title a {
  text-decoration: none;
  color: var(--project-color-main);
}
.notification-list-item .notification-content .notification-title a:hover, .unread-notification-list-item .notification-content .notification-title a:hover {
  text-decoration: underline;
}
@media (max-width: 768px) {
  .notification-list-item .notification-content .notification-title, .unread-notification-list-item .notification-content .notification-title {
    padding: 0px;
    margin-top: var(--s3);
    grid-row: 2/3;
    display: flex;
    flex-wrap: wrap;
  }
}
.notification-list-item .notification-content .notification-date, .unread-notification-list-item .notification-content .notification-date {
  grid-column: 2/3;
  display: flex;
  color: var(--project-color-secondary);
  font-weight: var(--font-weight-regular);
  font-size: var(--font-size-regular);
  letter-spacing: 0;
  line-height: var(--s6);
  padding: var(--s1) var(--s3) 0 0;
}
@media (max-width: 768px) {
  .notification-list-item .notification-content .notification-date, .unread-notification-list-item .notification-content .notification-date {
    display: none;
  }
}
.notification-list-item .notification-delete, .unread-notification-list-item .notification-delete {
  display: flex;
  justify-content: flex-end;
  padding-top: var(--s1);
}
@media (max-width: 768px) {
  .notification-list-item .notification-delete, .unread-notification-list-item .notification-delete {
    padding-top: 0;
  }
}

.unread-notification-list-item {
  border: var(--s1px) solid var(--color-orange-light);
  box-shadow: none;
}
.unread-notification-list-item:hover {
  border: var(--s1px) solid var(--color-orange-light);
}

.extended-tab-list {
  margin: 0px;
}
