:root {
  --icon-variant-color: #212121;
  --icon-tripane-inactive-fill: var(--color-grey);
  --icon-tripane-inactive-fill-hover: var(--color-white);
  --icon-red-error: var(--color-red-error-dark-mode);
}

.theme-light {
  --icon-variant-color: #FFFFFF;
  --icon-tripane-inactive-fill: var(--color-grey-medium);
  --icon-tripane-inactive-fill-hover: var(--color-black);
  --icon-red-error: var(--color-red-error-light-mode);
}

svg.icon path,
svg.icon polygon {
  fill: transparent;
  transition: all 0.2s ease-in-out;
}
svg.icon path.icon-white,
svg.icon polygon.icon-white {
  fill: var(--icon-primary-color);
}
svg.icon.fill path.fill, svg.icon.fill polygon.fill {
  fill: var(--icon-primary-color);
  transition: fill var(--transition);
}
svg.icon.outline path.outline {
  fill: var(--icon-primary-color);
  transition: fill var(--transition);
}
svg.icon.icon-black.fill path.fill, svg.icon.icon-black.fill polygon.fill {
  fill: var(--color-black);
}
svg.icon.icon-black.outline path.outline {
  fill: var(--color-black);
}
svg.icon.hover-black.fill:not(.no-hover):not(.menu-active):hover path.fill {
  fill: var(--color-black);
}
svg.icon.hover-black.outline:not(.no-hover):not(.menu-active):hover path.outline {
  fill: var(--color-black);
}
svg.icon.icon-error.fill path.fill, svg.icon.icon-error.fill polygon.fill {
