  .stepindicator-container.width-60 {
    margin-right: 10%;
  }
}

@media screen and (min-width: 768px) {
  .stepindicator-container.width-80 {
    margin-right: 13.3333333333%;
  }
}

@media screen and (min-width: 768px) {
  .stepindicator-container.width-100 {
    margin-right: 16.6666666667%;
  }
}

.rc-steps {
  color: var(--project-color-secondary);
  display: flex;
  justify-content: space-between;
}
.rc-steps div {
  box-sizing: border-box;
}
.rc-steps .rc-steps-item-container {
  display: grid;
  flex: 1;
  gap: 0 0;
  grid-template-areas: "subway-icon subway-tail" "desc desc";
  grid-template-columns: var(--s4) 1fr;
  grid-template-rows: repeat(2, auto);
  width: 100%;
}
.rc-steps .rc-steps-item-container .rc-steps-item-icon {
  background: var(--progress-step-color-inactive);
  font-size: 0;
  grid-area: subway-icon;
}
.rc-steps .rc-steps-item-container .rc-steps-item-tail {
  background: var(--progress-step-color-inactive);
  display: block;
  grid-area: subway-tail;
  height: var(--s2px);
  margin-top: var(--s2);
  transform: translateY(-50%);
}
.rc-steps .rc-steps-item-container .rc-steps-item-content {
  font-size: var(--font-size-s);
  grid-area: desc;
  padding-top: var(--s1);
}
.rc-steps .rc-steps-item-container .rc-steps-item-content .rc-steps-item-title {
  display: none;
}

.rc-steps-item-icon {
  border: var(--s2px) solid var(--progress-step-color-inactive);
  border-radius: var(--s4);
  height: var(--s4);
  margin-right: var(--s2);
  text-align: center;
  transition: background-color var(--transition), border-color var(--transition);
  width: var(--s4);
}

.rc-steps-item {
  width: 100%;
}
.rc-steps-item.rc-steps-item-active .rc-steps-item-icon {
  background: transparent;
  border: var(--s2px) solid var(--progress-step-color-active);
}
.rc-steps-item.rc-steps-item-finish .rc-steps-item-container .rc-steps-item-tail {
  background-color: var(--progress-step-color-active);
}
.rc-steps-item.rc-steps-item-finish .rc-steps-item-icon {
  background-color: var(--progress-step-color-active);
  border: var(--s2px) solid var(--progress-step-color-active);
}
.rc-steps-item:last-child .rc-steps-item-container {
  grid-template-areas: "subway-icon subway-icon" "desc desc";
}
