.card-message {
  align-items: center;
  background-color: var(--navbar-color);
  border-radius: var(--border-radius-half);
  box-shadow: var(--table-shadow);
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 250px;
  padding: var(--content-spacing);
  text-align: center;
}
.card-message .note-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: var(--s8) 25% var(--s8) 25%;
}
.card-message .note-container .note-with-links {
  color: var(--project-color-main);
  display: block;
  font-family: var(--font-family);
  font-size: var(--font-size-l);
  font-weight: var(--font-weight-bold);
  letter-spacing: 0;
  line-height: var(--s8);
  text-align: center;
  margin: 0;
}
.card-message .note-container .note-with-links a {
  color: var(--color-red);
  text-decoration: underline;
}
.card-message .note {
  display: flex;
  color: var(--project-color-main);
  font-family: var(--font-family);
  font-size: var(--font-size-l);
  font-weight: var(--font-weight-bold);
  letter-spacing: 0;
  line-height: var(--s8);
  text-align: center;
  margin: 0;
}
