.multi-select-container {
  width: 100%;
  background: var(--input-background);
  border: var(--border-size-standard) solid var(--input-border);
  border-top: none;
  border-radius: var(--border-radius);
  border-top-right-radius: 0;
  border-top-left-radius: 0;
  font-size: var(--font-size-regular);
  font-weight: var(--font-weight-regular);
  line-height: var(--input-lineheight);
  position: absolute;
  top: 50px;
  width: 100%;
  z-index: 100;
}
.multi-select-container > .scrollable {
  overflow: hidden;
  overflow-y: auto;
  max-height: 450px;
  min-height: var(--s12);
  position: relative;
}
.multi-select-container > div > div {
  padding: var(--s3);
}
.multi-select-container .multi-select-action-button-container {
  border-top: var(--border-size-standard) solid var(--input-border);
}
.multi-select-container .multi-select-action-button-container > button {
  margin: var(--s3);
  padding: var(--s3);
}
.multi-select-container .option-row:hover {
  background-color: #2a0f00;
}
.multi-select-container .option-row:hover .option-label label {
  color: var(--color-white);
}
.multi-select-container .selected-option {
  background-color: #2a0f00;
}
.multi-select-container .selected-option .option-label label {
  color: var(--color-white);
}
.multi-select-container .option-label {
  display: flex;
  flex-direction: row;
  width: 100%;
}
.multi-select-container .option-label .select-tile-checkbox {
  width: 15%;
}
.multi-select-container .option-label .select-tile-checkbox span,
.multi-select-container .option-label .select-tile-checkbox input {
  top: 0px;
}
.multi-select-container .option-label label {
  margin-bottom: 0px;
  margin-top: var(--s1);
  color: #bbbbbb;
}
