.notification-bell-menu {
  background: var(--menuitem-background);
}
.notification-bell-menu:hover {
  background: var(--menuitem-background-active);
}
.notification-bell-menu .notification-badge-container {
  display: flex;
  flex-direction: row;
  justify-content: left;
  justify-items: left;
  position: relative;
  top: var(--s3);
  height: 0px;
  z-index: 100;
  margin-left: var(--s13);
}
.notification-bell-menu .notification-badge-container .notification-badge {
  width: var(--s5);
  height: var(--s5);
  border-radius: var(--s6);
  font-size: var(--font-size-xxs);
  line-height: var(--s5);
  text-align: center;
  background-color: var(--color-orange);
  color: var(--color-white);
}
