.character-count-container {
  align-items: center;
  color: var(--label-color);
  display: flex;
  font-size: var(--font-size-s);
  justify-content: flex-end;
  opacity: 0;
  padding-top: var(--s3);
  transition: all 0.2s;
}

.character-count-alert {
  margin: 0 var(--s2);
}

.textarea-container.show-count + .helper .character-count-container {
  opacity: 1;
}

.text-input-container.show-count .character-count-container {
  opacity: 1;
}
