:root {
  --chart-background-color: #212121;
  --chart-secondary-color: #DEDEDE;
  --chart-text-color: #FFFFFF;
  --chart-text-family: "PwC Helvetica Neue", helvetica, arial, sans-serif;
  --chart-text-size: 0.875rem;
  --chart-bar-blue: #0089EB;
  --chart-bar-gold: #FFB600;
  --chart-bar-desert-rose: #D93954;
  --chart-axis-label-color: #BBBBBB;
  --chart-grid-line-color: #464646;
}

.theme-light {
  --chart-background-color: #FFFFFF;
  --chart-secondary-color: #464646;
  --chart-text-color: #212121;
  --chart-axis-label-color: #464646;
  --chart-grid-line-color: #BBBBBB;
}
