body {
  margin: 0;
}

.report-landing-page {
  background-color: var(--color-page-background);
  display: flex;
  flex-direction: column;
}
.report-landing-page button[aria-label]:focus {
  outline: 1px auto -webkit-focus-ring-color;
}
.report-landing-page .report-landing-page__content {
  padding: var(--container-spacing);
  display: flex;
  flex-direction: column;
  color: var(--project-color-main);
  margin-bottom: var(--s12);
}
.report-landing-page .report-landing-page__content .report-landing-page__headline {
  flex: 1 1 auto;
  color: var(--heading-primary);
  font-family: var(--font-default);
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-bold);
  letter-spacing: 0;
  line-height: var(--lineheight-xl);
  margin-bottom: var(--s6);
}
.report-landing-page .report-landing-page__content .report-landing-page__report-list-container {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
}
.report-landing-page .report-landing-page__content .report-landing-page__card {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 2px;
  background-color: var(--background-primary);
  box-shadow: var(--card-shadow);
  padding: var(--content-spacing);
  margin: 0rem 0rem var(--s8) 0rem;
}
.report-landing-page .report-landing-page__content .report-landing-page__card .report-landing-page__note-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 25% 2rem 25%;
}
.report-landing-page .report-landing-page__content .report-landing-page__card .report-landing-page__note-container .report-landing-page__note-with-links {
  display: block;
  color: inherit;
  font-family: var(--font-family);
  font-size: calc(var(--s5) + var(--s2px));
  font-weight: var(--font-weight-bold);
  letter-spacing: 0;
  line-height: var(--lineheight-l);
  text-align: center;
  margin: 0;
}
.report-landing-page .report-landing-page__content .report-landing-page__card .report-landing-page__note-container .report-landing-page__note-with-links a {
  color: var(--color-red);
  text-decoration: underline;
}
.report-landing-page .report-landing-page__content .report-landing-page__card .report-landing-page__note {
  display: flex;
  color: inherit;
  font-family: var(--font-family);
  font-size: calc(var(--s5) + var(--s2px));
  font-weight: var(--font-weight-bold);
  letter-spacing: 0;
  line-height: var(--lineheight-l);
  text-align: center;
  margin: 0;
}
.report-landing-page .report-landing-page__content .report-landing-page__card button.report-landing-page__nav-link-button:hover {
  text-decoration: underline;
}
.report-landing-page .report-landing-page__content .report-landing-page__card button.report-landing-page__nav-link-button:active {
  color: var(--color-orange-theme);
}
.report-landing-page .report-landing-page__content .report-landing-page__card button.report-landing-page__nav-link-button:focus:active {
  background-color: transparent;
}
.report-landing-page .report-landing-page__content .report-landing-page__card button.report-landing-page__nav-link-button:disabled {
  color: var(--color-grey);
}
