.report-view-port {
  color: var(--project-color-main);
  display: flex;
  flex-flow: column;
  height: auto;
  box-shadow: 1px 0 0 0 #2d2d2d;
  background: var(--color-black);
  border-top: 1px solid var(--color-grey-medium);
}
.report-view-port .report-pages {
  border-left: 1px solid var(--color-grey-medium);
  background-color: var(--color-grey-darker-alt);
  flex-grow: 1;
}

.report-builder .loader-contain {
  width: 100%;
}
