.icon-button {
  background-color: transparent;
  cursor: pointer;
  appearance: none;
  border-style: none;
  border-width: 0;
  border-color: transparent;
  padding: 0;
  display: flex;
}
.icon-button svg {
  height: calc(var(--s5) - var(--s2px));
  width: calc(var(--s5) - var(--s2px));
  pointer-events: none;
}
@media (max-width: 768px) {
  .icon-button svg {
    height: var(--s3);
    width: var(--s3);
  }
}
.icon-button svg[aria-label=asc], .icon-button svg[aria-label=desc] {
  height: var(--s4);
  width: var(--s4);
  pointer-events: none;
}
