.report-list-item-menu-password {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1rem;
  font-family: var(--font-family);
  font-size: 1rem;
}
.report-list-item-menu-password .divider {
  border-color: var(--color-grey-medium);
  width: 100%;
}
.report-list-item-menu-password .menu-title {
  color: var(--text-primary);
  font-weight: bold;
  margin-bottom: 0.75rem;
}
.report-list-item-menu-password .report-password {
  color: var(--color-grey);
}
.report-list-item-menu-password .menu-button-container {
  display: flex;
  justify-content: space-between;
}
.report-list-item-menu-password .menu-button-container > *:not(:last-child) {
  margin-right: 1.125rem;
}
