.textarea-fieldset {
  padding-bottom: 1.875rem;
  position: relative;
}
.textarea-fieldset .textarea-container {
  background: var(--input-background);
  border: var(--border-size-standard) solid transparent;
  border-radius: var(--border-radius);
  display: flex;
  min-height: var(--s17);
  position: relative;
  transition: padding var(--transition);
  width: 100%;
}
.textarea-fieldset .textarea-container.focus {
  border-color: var(--input-border-active);
  outline: none;
}
.textarea-fieldset .textarea-container.label-lifted {
  padding-bottom: var(--s10);
}
.textarea-fieldset .textarea-container.label-lifted .text-area-control-label {
  color: var(--label-color-focused);
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-regular);
  padding: var(--s3) 0 var(--s4) var(--s3);
  transform: translate3d(0, -25%, 0);
}
.textarea-fieldset .textarea-container.label-lifted .edit-icon {
  opacity: 0;
}
.textarea-fieldset .textarea-container.basic {
  background: none;
  border: 0;
  flex-direction: column;
  padding-bottom: var(--s2);
}
.textarea-fieldset .textarea-container.basic .text-area-control-label {
  color: var(--input-color);
  font-size: var(--font-size-regular);
  font-weight: var(--font-weight-regular);
  margin-right: var(--s1);
  padding: var(--s2) 0 var(--s3) 0;
  position: relative;
}
.textarea-fieldset .textarea-container.basic .helper-text {
  font-size: var(--font-size-s);
  font-weight: var(--font-weight-regular);
  margin-top: var(--s2);
}
.textarea-fieldset .textarea-container.basic .edit-icon {
  opacity: 0;
}
.textarea-fieldset .textarea-container.basic .text-area {
  background: var(--input-background);
  border: var(--border-size-standard) solid transparent;
  border-radius: var(--border-radius);
  font-family: var(--font-default);
  font-size: var(--font-size-regular);
  font-weight: var(--font-weight-regular);
  margin-right: var(--s3);
  min-height: var(--s12);
  padding: var(--s3);
  top: var(--s2);
}
.textarea-fieldset .textarea-container.basic .text-area:-webkit-autofill {
  caret-color: var(--input-color);
  margin-bottom: 0;
  margin-top: 0;
  padding-bottom: 0;
}
.textarea-fieldset .textarea-container.basic.focus .text-area {
  border: var(--border-size-standard) solid var(--input-border-active);
}
.textarea-fieldset .textarea-container .text-area {
  scrollbar-color: var(--color-grey-variant) var(--scrollbar-track-color);
  scrollbar-width: thin;
  /* stylelint-disable-next-line scss/selector-no-redundant-nesting-selector */
  background: transparent;
  border: var(--border-size-standard) solid transparent;
  border-radius: var(--border-radius);
  box-sizing: border-box;
  color: var(--input-color);
  font-family: var(--font-default);
  font-size: var(--font-size-l);
  font-weight: var(--font-weight-regular);
  line-height: var(--lineheight-l);
  margin-top: var(--sn2px);
  padding-left: var(--s3);
  position: relative;
  resize: none;
  top: var(--s6);
  width: 100%;
}
.textarea-fieldset .textarea-container .text-area::-webkit-scrollbar {
  border-radius: var(--s2);
  height: var(--s2);
  width: var(--s2);
}
.textarea-fieldset .textarea-container .text-area::-webkit-scrollbar-track {
  background: var(--color-grey);
}
.textarea-fieldset .textarea-container .text-area::-webkit-scrollbar-thumb {
  background: var(--color-grey);
  border-radius: var(--s2);
}
.textarea-fieldset .textarea-container .text-area::-webkit-scrollbar-thumb:hover {
  background: var(--color-grey-dark);
}
.textarea-fieldset .textarea-container .text-area {
  -ms-scrollbar-3dlight-color: var(--color-grey);
  -ms-scrollbar-face-color: var(--color-grey);
  scrollbar-face-color: var(--color-grey);
  -ms-scrollbar-track-color: var(--color-grey);
  scrollbar-track-color: var(--color-grey);
}
@media screen and (max-width: 767px) {
  .textarea-fieldset .textarea-container .text-area {
    font-size: var(--font-size-regular);
    line-height: var(--lineheight-regular);
  }
}
.textarea-fieldset .textarea-container .text-area.focus-visible {
  box-shadow: none;
}
.textarea-fieldset .textarea-container .text-area:focus {
  border-color: transparent;
  border-width: var(--border-size-standard);
  outline: none;
}
.textarea-fieldset .textarea-container.error {
  border-color: var(--color-error);
}
.textarea-fieldset .textarea-container .text-area-control-label {
  color: var(--input-color-placeholder);
  font-size: var(--font-size-l);
  padding: var(--s4) 0 0 var(--s4);
  position: absolute;
  top: var(--s1);
  transition: all var(--transition);
}
.textarea-fieldset .textarea-container .text-area + .text-area-control-label {
  font-weight: var(--font-weight-regular);
}
.textarea-fieldset .textarea-container .edit-icon {
  height: var(--s5);
  margin-right: var(--s3);
  margin-top: var(--s6);
  pointer-events: none;
  position: absolute;
  right: 0;
  transition: all var(--transition);
  width: var(--s5);
}
.textarea-fieldset .textarea-container .edit-icon svg {
  overflow: visible;
  vertical-align: unset;
}
.textarea-fieldset .textarea-container .edit-icon .fill {
  fill: var(--color-grey-alt-lighter);
}
.textarea-fieldset .helper-flex {
  display: flex;
  justify-content: space-between;
}
.textarea-fieldset .helper-text {
  align-items: center;
  color: var(--label-color);
  display: flex;
  font-size: var(--font-size-s);
  font-weight: var(--font-weight-regular);
  margin-top: var(--s3);
}
.textarea-fieldset .helper-text .helper-icon {
  margin-right: var(--s2);
}
.textarea-fieldset .a-form-error {
  align-items: flex-start;
  color: var(--color-error);
  display: flex;
  font-size: var(--font-size-s);
  line-height: var(--lineheight-s);
  margin-bottom: var(--s3);
}
.textarea-fieldset .a-form-error .input-error-icon {
  flex: 0 0 var(--s4);
  margin-right: var(--s2);
  margin-top: var(--s2px);
  width: var(--s4);
}
