.extended-tabs .scrollable {
  overflow-y: auto;
}

.extended-tab-list {
  align-items: flex-end;
  background-color: var(--background-secondary);
  display: flex;
  height: var(--s14);
  list-style: none;
  margin-bottom: var(--s8);
  padding: 0;
  width: min-content;
}
.extended-tab-list:focus {
  outline: 1px auto var(--text-primary);
}
@media screen and (max-width: 991px) {
  .extended-tab-list {
    padding-right: 0.375rem;
  }
}
@media screen and (min-width: 768px) {
  .extended-tab-list {
    width: auto;
  }
}

.extended-tab-list-item {
  background-color: var(--tab-background-color);
  border: var(--s2px) solid var(--tab-background-color);
  border-radius: var(--s2px) var(--s2px) 0 0;
  color: var(--tab-text-color);
  cursor: pointer;
  font-size: var(--font-size-regular);
  line-height: var(--s11);
  margin-left: 0.375rem;
  padding: 0 var(--s6);
  white-space: nowrap;
}
.extended-tab-list-item:hover {
  text-decoration: underline;
}

.extended-tab-list-active {
  background-color: var(--tab-background-color-active);
  border-color: var(--tab-background-color-active);
  color: var(--tab-text-color-active);
}

.js-focus-visible .extended-tab-list-item.focus-visible:focus {
  border-color: var(--tab-border-keyboard-focus);
  border-bottom-color: var(--tab-background-color);
  border-width: var(--s2px);
  box-shadow: none;
  text-decoration: underline;
}
.js-focus-visible .extended-tab-list-active.focus-visible:focus {
  border-bottom-color: var(--tab-background-color-active);
  text-decoration: underline;
}

.extended-tab-list-inactive {
  color: var(--color-disabled);
  cursor: default;
}
