.report-list-item {
  background-color: var(--background-primary);
  box-shadow: var(--card-shadow);
  border-radius: 2px;
  display: flex;
  padding: var(--content-spacing);
  border: 1px solid transparent;
}
.report-list-item label {
  margin: 0;
}
.report-list-item:hover {
  border: var(--s1px) solid var(--color-grey-medium);
  box-shadow: none;
}
.report-list-item.archived {
  border: var(--s2px) solid var(--color-grey-darker);
  border-radius: var(--s2px);
  background-color: var(--color-grey-darker-alt);
  box-shadow: unset;
}
.report-list-item.archived .report-name.archived {
  color: var(--color-grey);
  font-family: var(--font-family);
  font-size: var(--font-size-regular);
  font-weight: var(--font-weight-bold);
  letter-spacing: 0;
  line-height: var(--lineheight-regular);
}
.report-list-item.archived .report-name,
.report-list-item.archived .report-format,
.report-list-item.archived .report-created {
  text-decoration: line-through;
}
.report-list-item.archived:hover {
  border: 2px solid var(--color-grey-darker);
  background-color: var(--color-grey-darker);
}
.report-list-item.archived:hover .report-actions button.archived {
  background-color: var(--color-grey-dark);
}
.report-list-item.archived:hover .report-actions button.archived svg > g[fill-rule*=evenodd] g[id*="Color/Gray/Black"] {
  fill: var(--color-orange-light);
}
.report-list-item.archived:hover .report-actions button.archived svg > g use#Mask {
  fill: var(--color-orange-light);
}
.report-list-item .report-details {
  width: 100%;
  display: grid;
  grid-template-columns: [name-col] 55% [date_actions] auto;
}
@media (max-width: 767px) {
  .report-list-item .report-details {
    display: flex;
    flex-flow: column;
  }
}
.report-list-item .report-details .report-title {
  grid-column: 1/2;
  display: flex;
  flex-flow: column;
  padding-right: 1rem;
}
.report-list-item .report-details .report-title .report-name-description {
  width: 100%;
}
.report-list-item .report-details .report-title .report-name-description .report-name:not(.archived) {
  color: var(--project-color-main);
  font-family: var(--font-family);
  font-size: var(--font-size-regular);
  font-weight: var(--font-weight-bold);
  letter-spacing: 0;
  line-height: var(--lineheight-regular);
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  display: -webkit-box;
  overflow: hidden;
}
@media (max-width: 992px) {
  .report-list-item .report-details .report-title .report-name-description .report-name:not(.archived) {
    -webkit-line-clamp: 2;
  }
}
.report-list-item .report-details .report-title .report-format-label {
  display: flex;
}
@media (max-width: 767px) {
  .report-list-item .report-details .report-title .report-format-label {
    flex-direction: column;
  }
}
.report-list-item .report-details .report-title .report-format-label .report-format {
  color: var(--color-grey);
  font-family: var(--font-family);
  font-size: var(--font-size-regular);
  font-weight: var(--font-weight-regular);
  letter-spacing: 0;
  line-height: var(--lineheight-regular);
}
.report-list-item .report-details .report-title .report-format-label .report-labels {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 0 0 0 var(--s3);
}
@media (max-width: 768 -1px) {
  .report-list-item .report-details .report-title .report-format-label .report-labels {
    margin: var(--s2) 0 0 0;
  }
}
.report-list-item .report-details .report-divider {
  border-color: var(--color-grey-medium);
  width: 100%;
}
.report-list-item .report-details .report-created-actions {
  display: flex;
  justify-content: space-between;
}
.report-list-item .report-details .report-created-actions.report-created-actions--failed {
  display: grid;
  grid-template-columns: [created] 45% [failure-message] 35% [actions] auto;
}
@media (max-width: 1200px) {
  .report-list-item .report-details .report-created-actions.report-created-actions--failed {
    grid-template-columns: [created] auto [failure-message] auto [actions] auto;
  }
}
@media (max-width: 768 -1px) {
  .report-list-item .report-details .report-created-actions.report-created-actions--failed {
    grid-template-columns: [created] auto [actions] auto;
    grid-template-rows: [created-actions] auto [failure-message] auto;
    row-gap: var(--s3);
  }
}
.report-list-item .report-details .report-created-actions .report-created {
  grid-column: 1/2;
  grid-row: 1/2;
  color: var(--color-grey);
  font-family: var(--font-family);
  font-size: var(--font-size-regular);
  font-weight: var(--font-weight-regular);
  letter-spacing: 0;
  line-height: var(--lineheight-regular);
  padding-right: var(--s4);
  display: flex;
  flex-direction: column;
}
.report-list-item .report-details .report-created-actions .report-created .report-created__label {
  display: flex;
  flex-wrap: wrap;
}
.report-list-item .report-details .report-created-actions .report-actions__failed {
  grid-column: 2/3;
  grid-row: 1/2;
  display: flex;
  align-self: flex-start;
}
@media (max-width: 768 -1px) {
  .report-list-item .report-details .report-created-actions .report-actions__failed {
    grid-column: 1/2;
    grid-row: 2/3;
  }
}
.report-list-item .report-details .report-created-actions .report-actions__failed > *:not(:last-child) {
  margin: 0 var(--s3) 0 0;
}
.report-list-item .report-details .report-created-actions .report-actions__failed .report-actions__failed__icon-container {
  display: flex;
  align-self: flex-start;
}
.report-list-item .report-details .report-created-actions .report-actions__failed .report-actions__failed__icon-container svg.icon-red-error.fill path.fill.always-dark {
  fill: var(--background-primary);
}
.report-list-item .report-details .report-created-actions .report-actions__failed span.report-actions__failed__error {
  display: flex;
  font-size: var(--font-size-s);
  font-weight: var(--font-weight-medium);
  color: var(--color-red-error-dark-mode);
  letter-spacing: 0;
  line-height: var(--lineheight-s);
}
.report-list-item .report-details .report-created-actions .report-actions {
  grid-column: 3/4;
  grid-row: 1/2;
  display: inline-flex;
  justify-content: flex-end;
  align-items: flex-start;
  margin-left: calc(var(--s4) + var(--s2px));
}
.report-list-item .report-details .report-created-actions .report-actions > *:not(:last-child) {
  margin-right: var(--s4);
}
@media (max-width: 768 -1px) {
  .report-list-item .report-details .report-created-actions .report-actions {
    grid-column: 2/3;
  }
}
.report-list-item .report-details .report-created-actions .report-actions .loader-container {
  align-items: baseline;
  margin-right: var(--s3);
  height: var(--s6);
}
