.report-desc {
  font-weight: normal;
  text-align: left;
  line-height: 27px;
  font-size: var(--font-size-regular);
  padding: 0 var(--s6) var(--s6) var(--s6);
}

#report-export.report-export-modal .modal-content,
#report-success.report-export-modal .modal-content {
  color: var(--project-color-main);
  border: var(--modal-border);
  border-radius: var(--border-radius-half);
  background-color: var(--background-primary);
}
#report-export.report-export-modal .modal-content button[aria-label]:focus,
#report-success.report-export-modal .modal-content button[aria-label]:focus {
  outline: 1px auto -webkit-focus-ring-color;
}
#report-export.report-export-modal .modal-content label,
#report-success.report-export-modal .modal-content label {
  margin-bottom: 0;
  display: flex;
}
#report-export.report-export-modal .modal-content .modal-footer,
#report-success.report-export-modal .modal-content .modal-footer {
  border-top-color: transparent;
}
#report-export.report-export-modal .modal-content .modal-header,
#report-success.report-export-modal .modal-content .modal-header {
  border-bottom-color: transparent;
  padding: var(--s6);
  display: grid;
  grid-template-columns: [modalTitle] 62% [closeButton] auto;
}
#report-export.report-export-modal .modal-content .modal-header button.close,
#report-success.report-export-modal .modal-content .modal-header button.close {
  grid-column: 2/3;
  padding: 0;
  margin: 0;
}
#report-export.report-export-modal .modal-content .modal-body,
#report-success.report-export-modal .modal-content .modal-body {
  padding: 0;
}
#report-export.report-export-modal .modal-content .modal-title,
#report-success.report-export-modal .modal-content .modal-title {
  grid-column: 1/2;
  font-size: calc(var(--font-size-xl) - var(--s2px));
  font-weight: var(--font-weight-bold);
  line-height: calc(var(--lineheight-xl) - var(--s2px));
}

@media (max-width: 1600px) {
  #report-export.report-export-modal,
#report-success.report-export-modal {
    width: 70%;
    max-width: none;
  }
}
@media (max-width: 1200px) {
  #report-export.report-export-modal,
#report-success.report-export-modal {
    width: 80%;
    max-width: none;
  }
}
@media (max-width: 992px) {
  #report-export.report-export-modal,
#report-success.report-export-modal {
    width: 90%;
    max-width: none;
  }
}
@media (max-width: 577px) {
  #report-export.report-export-modal,
#report-success.report-export-modal {
    width: auto;
    max-width: unset;
  }
}
#report-export.report-export-modal .modal-content .report-export-modal-button-row,
#report-export.report-export-modal .modal-content .report-success-modal-button-row,
#report-success.report-export-modal .modal-content .report-export-modal-button-row,
#report-success.report-export-modal .modal-content .report-success-modal-button-row {
  display: flex;
  flex: 1 1 auto;
  justify-content: flex-end;
  padding: var(--s6) var(--s6) 0 var(--s6);
  background-color: var(--color-page-background);
}
#report-export.report-export-modal .modal-content .report-export-modal-button-row button.report-export-modal-button-row__close-button,
#report-export.report-export-modal .modal-content .report-export-modal-button-row button.report-success-modal-button-row__close-button,
#report-export.report-export-modal .modal-content .report-success-modal-button-row button.report-export-modal-button-row__close-button,
#report-export.report-export-modal .modal-content .report-success-modal-button-row button.report-success-modal-button-row__close-button,
#report-success.report-export-modal .modal-content .report-export-modal-button-row button.report-export-modal-button-row__close-button,
#report-success.report-export-modal .modal-content .report-export-modal-button-row button.report-success-modal-button-row__close-button,
#report-success.report-export-modal .modal-content .report-success-modal-button-row button.report-export-modal-button-row__close-button,
#report-success.report-export-modal .modal-content .report-success-modal-button-row button.report-success-modal-button-row__close-button {
  display: flex;
  justify-self: flex-end;
  background: none;
  padding: 0;
  margin: 0;
  border: none;
}
#report-export.report-export-modal .modal-content .modal-header button.close,
#report-success.report-export-modal .modal-content .modal-header button.close {
  color: var(--button-primary-color);
  text-shadow: none;
  font-size: 2rem;
  opacity: 1;
}
#report-export.report-export-modal .modal-content .modal-header button.close:focus,
#report-success.report-export-modal .modal-content .modal-header button.close:focus {
  outline: 1px auto -webkit-focus-ring-color;
}
#report-export.report-export-modal .modal-content .modal-header button.close:hover,
#report-success.report-export-modal .modal-content .modal-header button.close:hover {
  outline: none;
  color: var(--button-primary-color-hover);
  opacity: 0.5;
}
#report-export.report-export-modal .modal-content .modal-header .modal-title h2,
#report-success.report-export-modal .modal-content .modal-header .modal-title h2 {
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-bold);
  line-height: var(--lineheight-xl);
  margin-bottom: var(--sn6);
}

/* Width */
#contents::-webkit-scrollbar {
  width: calc(var(--s2) - var(--s2px));
}

/* Track */
#contents::-webkit-scrollbar-track {
  background: var(--table-background-color);
}

/* Handle */
#contents::-webkit-scrollbar-thumb {
  background: var(--color-grey-medium);
  border-radius: 10px !important;
}

/* Handle on hover */
#contents::-webkit-scrollbar-thumb:hover {
  background: var(--color-grey-dark);
}
