.preview-placeholder {
  min-width: 1056px;
  min-height: 816px;
  border: 1px dashed var(--color-grey-variant);
  border-radius: var(--s2px);
  background-color: var(--background-primary);
  margin-bottom: var(--s4);
  display: flex;
  justify-content: center;
}
.preview-placeholder .report-builder-iconHeadlineNote {
  padding: var(--s20) 0 var(--s25) 0;
  background-color: inherit;
}
.preview-placeholder .report-builder-iconHeadlineNote .title {
  font-size: calc(var(--font-size-xl) + var(--s2px));
  margin-bottom: 0;
}
.preview-placeholder .report-builder-iconHeadlineNote .note-for-full-report-preview-error, .preview-placeholder .report-builder-iconHeadlineNote .note-for-tile-preview-error {
  font-size: calc(var(--font-size-regular) + var(--s2px));
  line-height: var(--lineheight-regular);
}
.preview-placeholder .report-builder-iconHeadlineNote .note-for-tile-preview-error {
  white-space: pre-line;
}
