:root {
  --tripane-spacing: var(--s8);
}

.tri-pane-container {
  padding: var(--s12) var(--s4);
}
@media screen and (min-width: 992px) {
  .tri-pane-container {
    padding-left: var(--s12);
    padding-right: var(--s12);
  }
}

.related-data.description-only, .tri-pane-container section:nth-of-type(1)[aria-label="description pane"], .tri-pane-container .tri-pane-header.description-only {
  margin: 0 auto;
  max-width: 43.75rem;
  width: 100%;
}

.tri-pane-container {
  display: grid;
  grid-gap: var(--tripane-spacing);
  grid-template-areas: "header-content" "pane-one" "pane-two" "pane-three";
  grid-template-columns: auto;
  grid-template-rows: auto;
}
@media screen and (min-width: 768px) {
  .tri-pane-container {
    grid-template-areas: "header-content header-content" "pane-one pane-one" "pane-two pane-three";
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto auto;
  }
}
@media screen and (min-width: 1200px) {
  .tri-pane-container {
    grid-template-areas: "header-content pane-two" "pane-one pane-two" "pane-one pane-three";
    grid-template-columns: 60% auto;
    grid-template-rows: auto auto 1fr;
  }
}
.tri-pane-container .padded {
  padding: var(--content-spacing);
}
.tri-pane-container section {
  animation-duration: 0.3s;
  animation-name: fadeIn;
}
.tri-pane-container .tri-pane-header {
  grid-area: header-content;
}
.tri-pane-container section:nth-of-type(1) {
  background-color: transparent;
  box-shadow: none;
  grid-area: pane-one;
}
@media screen and (max-width: 1199px) {
  .tri-pane-container section:nth-of-type(1) {
    scrollbar-color: var(--color-grey-variant) var(--scrollbar-track-color);
    scrollbar-width: thin;
    /* stylelint-disable-next-line scss/selector-no-redundant-nesting-selector */
    margin-bottom: var(--s10);
    overflow-x: auto;
    padding-bottom: 0.3125rem;
  }
  .tri-pane-container section:nth-of-type(1)::-webkit-scrollbar {
    border-radius: var(--s1);
    height: var(--s1);
    width: var(--s1);
  }
  .tri-pane-container section:nth-of-type(1)::-webkit-scrollbar-track {
    background: var(--scrollbar-track-color);
  }
  .tri-pane-container section:nth-of-type(1)::-webkit-scrollbar-thumb {
    background: var(--scrollbar-handle-color);
    border-radius: var(--s1);
  }
  .tri-pane-container section:nth-of-type(1)::-webkit-scrollbar-thumb:hover {
    background: var(--scrollbar-handle-color-hover);
  }
  .tri-pane-container section:nth-of-type(1) {
    -ms-scrollbar-3dlight-color: var(--scrollbar-handle-color);
    -ms-scrollbar-face-color: var(--scrollbar-handle-color);
    scrollbar-face-color: var(--scrollbar-handle-color);
    -ms-scrollbar-track-color: var(--scrollbar-track-color);
    scrollbar-track-color: var(--scrollbar-track-color);
  }
}
.tri-pane-container section:nth-of-type(1) .highcharts-background {
  fill: transparent;
}
.tri-pane-container section:nth-of-type(1)[aria-label="description pane"] {
  box-shadow: none;
  padding: var(--content-spacing) 0;
}
.tri-pane-container section:nth-of-type(2) {
  background-color: var(--tripane-desc-background);
  box-shadow: var(--card-shadow);
  grid-area: pane-two;
  overflow-x: auto;
}
@media screen and (min-width: 1200px) {
  .tri-pane-container section:nth-of-type(2) {
    margin-top: var(--s3);
  }
}
.tri-pane-container section:nth-of-type(3) {
  background-color: var(--tripane-desc-background);
  box-shadow: var(--card-shadow);
  grid-area: pane-three;
  overflow-x: auto;
  padding: var(--content-spacing);
}
.tri-pane-container section:nth-of-type(3) .ck-widget {
  scrollbar-color: var(--color-grey-variant) var(--scrollbar-track-color);
  scrollbar-width: thin;
  /* stylelint-disable-next-line scss/selector-no-redundant-nesting-selector */
  padding-bottom: var(--s2);
}
.tri-pane-container section:nth-of-type(3) .ck-widget::-webkit-scrollbar {
  border-radius: var(--s1);
  height: var(--s1);
  width: var(--s1);
}
.tri-pane-container section:nth-of-type(3) .ck-widget::-webkit-scrollbar-track {
  background: #eee;
}
.tri-pane-container section:nth-of-type(3) .ck-widget::-webkit-scrollbar-thumb {
  background: #aaa;
  border-radius: var(--s1);
}
.tri-pane-container section:nth-of-type(3) .ck-widget::-webkit-scrollbar-thumb:hover {
  background: var(--input-background);
}
.tri-pane-container section:nth-of-type(3) .ck-widget {
  -ms-scrollbar-3dlight-color: #aaa;
  -ms-scrollbar-face-color: #aaa;
  scrollbar-face-color: #aaa;
  -ms-scrollbar-track-color: #eee;
  scrollbar-track-color: #eee;
}

.panes-1 .tri-pane-container {
  grid-template-areas: "header-content" "pane-one";
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
}
@media screen and (min-width: 768px) {
  .panes-1 .tri-pane-container {
    grid-template-areas: "header-content" "pane-one";
    grid-template-columns: 1fr;
    grid-template-rows: auto;
  }
}
.panes-1 .tri-pane-container .description-only + .pane.padded {
  padding-top: 0;
}

.panes-2 .tri-pane-container {
  grid-template-areas: "header-content" "pane-one" "pane-two";
  grid-template-columns: auto;
  grid-template-rows: auto;
}
@media screen and (min-width: 768px) {
  .panes-2 .tri-pane-container {
    grid-template-areas: "header-content header-content" "pane-one pane-one" "pane-two pane-two";
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto auto;
  }
}
@media screen and (min-width: 1200px) {
  .panes-2 .tri-pane-container {
    grid-template-areas: "header-content pane-two" "pane-one pane-two" "pane-one pane-two";
    grid-template-columns: 60% auto;
    grid-template-rows: auto 1fr;
  }
}

svg.highcharts-root {
  width: 100%;
}

.graph-legend {
  font-size: 10px;
}

.tri-pane-control {
  align-items: center;
  border-right: var(--border-standard);
  flex-direction: row;
  height: 100%;
  padding-right: var(--s2);
}
@media screen and (min-width: 992px) {
  .tri-pane-control {
    padding-right: var(--s10);
  }
}
.tri-pane-control .wrapper svg {
  cursor: pointer;
}
.tri-pane-control .wrapper [type=checkbox] {
  opacity: 0;
  width: 0;
}
.tri-pane-control .wrapper label {
  display: inline-flex;
  padding: var(--s1) var(--s3);
}
.tri-pane-control .wrapper label:not(:last-of-type) {
  border-right: var(--s1px) solid var(--tooltip-border-color);
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@media screen and (min-width: 768px) {
  .freeze .tri-pane-container {
    grid-template-areas: "header-content" "pane-one" "pane-two" "pane-three";
    grid-template-columns: 1fr;
    grid-template-rows: auto;
  }
  .freeze .tri-pane-container section:nth-of-type(2) {
    margin-top: 0;
  }
}
@media screen and (min-width: 1200px) {
  .freeze .tri-pane-container {
    grid-template-areas: "header-content header-content" "pane-one pane-one" "pane-two pane-three";
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto auto;
  }
}
@media screen and (min-width: 1600px) {
  .freeze .tri-pane-container {
    grid-template-areas: "header-content pane-two" "pane-one pane-two" "pane-one pane-three";
    grid-template-columns: 60% auto;
    grid-template-rows: auto auto 1fr;
  }
  .freeze .tri-pane-container section:nth-of-type(1) {
    background-color: transparent;
    overflow: hidden;
  }
  .freeze .tri-pane-container section:nth-of-type(1) .highcharts-background {
    fill: transparent;
  }
  .freeze .tri-pane-container section:nth-of-type(2) {
    margin-top: var(--s3);
  }
}

[aria-label^=chart],
[aria-label^=description] {
  background-color: var(--tripane-desc-background);
}

[aria-label^=description] {
  min-height: 380px;
}

.related-data.description-only {
  padding: var(--content-spacing) 0;
}
