.header {
  border-bottom: var(--s1px) solid var(--border-primary);
  display: flex;
  height: var(--s16);
}
.header .header-logo {
  align-items: center;
  display: flex;
  justify-content: center;
  width: var(--s21);
}
@media screen and (min-width: 768px) {
  .header .header-logo {
    border-right: var(--s1px) solid var(--border-primary);
  }
}
@media screen and (max-width: 767px) {
  .header .header-logo {
    justify-content: flex-start;
    width: 200px;
  }
}
.header .header-content {
  align-items: center;
  display: flex;
  justify-content: space-between;
  width: 100%;
}
