.text-input-fieldset {
  padding-bottom: var(--s8);
  position: relative;
}
.text-input-fieldset .helper-text {
  align-items: center;
  color: var(--label-color);
  display: flex;
  font-size: var(--font-size-s);
  font-weight: var(--font-weight-regular);
  margin-top: var(--s3);
}
.text-input-fieldset .text-input-container {
  background: var(--input-background);
  border: var(--border-standard);
  border-radius: var(--border-radius);
  color: var(--input-color);
  display: flex;
  overflow: hidden;
  width: 100%;
}
.text-input-fieldset .text-input-container.allow-overflow {
  overflow: unset;
}
.text-input-fieldset .text-input-container.focus {
  border-color: var(--input-border-active);
  outline: none;
}
.text-input-fieldset .text-input-container ::placeholder {
  color: var(--input-color-placeholder);
  opacity: 1;
}
.text-input-fieldset .text-input-container.label-lifted .text-input-control-label {
  color: var(--label-color-focused);
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-medium);
  padding: var(--s2) 0 0 var(--s3);
  transform: translate3d(0, -15%, 0);
}
.text-input-fieldset .text-input-container.label-lifted .text-input {
  transform: translate3d(0, 10%, 0);
}
.text-input-fieldset .text-input-container.label-lifted .edit-icon {
  opacity: 0;
}
.text-input-fieldset .text-input-container.basic {
  background: none;
  border: 0;
  flex-direction: column;
}
.text-input-fieldset .text-input-container.basic label {
  color: var(--input-color);
  font-size: var(--font-size-regular);
  font-weight: var(--font-weight-medium);
  padding: var(--s2) 0;
  position: relative;
}
.text-input-fieldset .text-input-container.basic label .helper-text {
  font-size: var(--font-size-s);
  font-weight: var(--font-weight-regular);
  margin-top: var(--s2);
}
.text-input-fieldset .text-input-container.basic label .helper-text .helper-icon {
  margin-right: var(--s2);
}
.text-input-fieldset .text-input-container.basic .edit-icon {
  opacity: 0;
}
.text-input-fieldset .text-input-container.basic .text-input {
  background: var(--input-background);
  border: var(--border-standard);
  border-radius: var(--border-radius);
  font-size: var(--font-size-regular);
  font-weight: var(--font-weight-regular);
  line-height: var(--input-lineheight);
  min-height: var(--s12);
  padding: var(--s3);
}
.text-input-fieldset .text-input-container.basic .text-input:-webkit-autofill {
  caret-color: var(--input-color);
  margin-bottom: 0;
  margin-top: 0;
  padding-bottom: 0;
}
.text-input-fieldset .text-input-container.basic.focus .text-input {
  border: var(--border-size-standard) solid var(--input-border-active);
}
.text-input-fieldset .text-input-container.basic .has-icon-left {
  bottom: var(--s9);
  height: var(--s10);
  left: var(--s3);
  pointer-events: none;
  position: absolute;
  width: var(--s10);
  z-index: 4;
}
.text-input-fieldset .text-input-container.basic .has-icon-left + .text-input {
  padding-left: var(--s10);
}
.text-input-fieldset .text-input-container .text-input {
  background-color: transparent;
  border-width: 0;
  box-sizing: border-box;
  color: var(--input-color);
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-bold);
  padding: var(--s4) 0 var(--s4) var(--s3);
  position: relative;
  width: 100%;
}
.text-input-fieldset .text-input-container .text-input.focus-visible {
  box-shadow: none;
}
.text-input-fieldset .text-input-container .text-input:focus {
  border: 0;
  outline: none;
}
.text-input-fieldset .text-input-container .text-input:-webkit-autofill {
  background-color: var(--input-background);
  border-color: var(--input-border);
  box-shadow: inset 0 0 0 624.9375rem var(--input-background);
  caret-color: var(--input-color);
  margin-top: var(--s4);
  outline: none;
  padding-top: 0;
  -webkit-text-fill-color: var(--input-color);
}
.text-input-fieldset .text-input-container .text-input:-webkit-autofill:focus {
  outline: none;
}
.text-input-fieldset .text-input-container .text-input:-webkit-autofill ~ .text-input-control-label {
  z-index: 1;
}
.text-input-fieldset .text-input-container.error .text-input:focus {
  border-color: var(--color-error);
}
.text-input-fieldset .text-input-container .text-input-control-label {
  color: var(--input-color-placeholder);
  display: inline-block;
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-bold);
  padding: var(--s4) 0 0 var(--s4);
  pointer-events: none;
  position: absolute;
  transition: all var(--transition);
}
.text-input-fieldset .text-input-container .edit-icon {
  height: var(--s5);
  margin-right: var(--s3);
  margin-top: var(--s5);
  pointer-events: none;
  right: 0;
  transition: all var(--transition);
}
.text-input-fieldset .text-input-container .edit-icon svg {
  overflow: visible;
  vertical-align: unset;
}
.text-input-fieldset .text-input-container .edit-icon .fill {
  fill: var(--input-icon-color);
}
.text-input-fieldset .text-input-container.small {
  box-sizing: border-box;
  height: var(--s12);
  min-height: var(--s12);
}
.text-input-fieldset .text-input-container.small .text-input-control-label {
  display: none;
}
.text-input-fieldset .text-input-container.small .text-input {
  font-size: var(--font-size-regular);
  font-weight: var(--font-weight-regular);
  min-height: unset;
}
.text-input-fieldset .text-input-container.small .text-input:-webkit-autofill {
  padding-top: 0;
}
.text-input-fieldset .text-input-container.small.focus.label-lifted .text-input, .text-input-fieldset .text-input-container.small.label-lifted .text-input {
  transform: translate3d(0, 0, 0);
}
.text-input-fieldset .text-input-container.small .edit-icon {
  margin-right: 0;
  margin-top: var(--s3);
  padding: 0 var(--s3);
}

.a-form-error {
  align-items: flex-start;
  color: var(--color-error);
  display: flex;
  font-size: var(--font-size-s);
  line-height: var(--lineheight-s);
  margin-bottom: var(--s3);
}
.a-form-error .input-error-icon {
  flex: 0 0 var(--s4);
  margin-right: var(--s2);
  margin-top: var(--s2px);
  width: var(--s4);
}

input:-internal-autofill-selected {
  background-color: transparent !important;
}
