.overlayedButton {
  background-color: var(--color-grey-dark);
  width: var(--s12);
  height: var(--s12);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border: 1px solid transparent;
}
.overlayedButton:hover:not(.archived) {
  border: 1px solid var(--color-grey-medium);
}
.overlayedButton.archived {
  background-color: var(--color-grey-darker);
}
.overlayedButton.archived:hover {
  border: 1px solid transparent;
}
.overlayedButton:not(:last-of-type) {
  margin-right: 1rem;
}
.overlayedButton svg {
  height: 1.5rem;
  width: 1.5rem;
  pointer-events: none;
}
