.report-list-item-menu-confirm-share {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: var(--s4);
  font-family: var(--font-family);
  font-size: var(--font-size-regular);
}
.report-list-item-menu-confirm-share .divider {
  border-color: var(--color-grey-medium);
  width: 100%;
}
.report-list-item-menu-confirm-share .menu-title {
  color: var(--text-primary);
  font-weight: var(--font-weight-bold);
  margin-bottom: var(--s3);
}
.report-list-item-menu-confirm-share .menu-note {
  color: var(--color-grey);
}
.report-list-item-menu-confirm-share .menu-button-container {
  display: flex;
  justify-content: space-between;
}
.report-list-item-menu-confirm-share .menu-button-container > *:not(:last-child) {
  margin-right: var(--s5);
}
