.report-section, .report-sub-section {
  border-top: 1px solid var(--color-grey-medium);
  display: flex;
  font-size: var(--font-size-xs);
  flex-direction: column;
  margin-left: 40px;
  margin-right: var(--s4);
  color: var(--project-color-main);
}
.report-section .section-title, .report-sub-section .section-title {
  color: inherit;
  font-size: var(--font-size-regular);
  width: 100%;
  padding-top: var(--s4);
  padding-bottom: var(--s4);
  display: flex;
  flex-direction: row;
}
.report-section .sections-pages, .report-sub-section .sections-pages {
  display: flex;
  flex-direction: row;
  margin-bottom: var(--s4);
  width: 101.5%;
}
.report-section .drop-zone-section, .report-sub-section .drop-zone-section, .report-section .drop-zone-section-show, .report-sub-section .drop-zone-section-show {
  width: 100%;
  border: 2px solid var(--color-gray);
  height: 300px;
  border-style: dotted;
  display: none;
}
.report-section .drop-zone-section-show, .report-sub-section .drop-zone-section-show {
  display: initial;
  border-style: dotted;
}
.report-section .report-builder-iconHeadlineNote, .report-sub-section .report-builder-iconHeadlineNote {
  background: inherit;
  justify-content: flex-start;
}
.report-section .report-builder-iconHeadlineNote .title, .report-sub-section .report-builder-iconHeadlineNote .title {
  font-size: var(--font-size-l);
  margin-bottom: 0;
}
.report-section .report-builder-iconHeadlineNote .note-for-no-filtered-results, .report-sub-section .report-builder-iconHeadlineNote .note-for-no-filtered-results {
  font-size: var(--font-size-regular);
  line-height: var(--lineheight-regular);
  white-space: pre-line;
}

.report-sub-section {
  border-top-color: var(--color-grey-medium);
}
