.report-index {
  color: var(--project-color-main);
  display: flex;
  background-color: var(--menuitem-separator);
  flex-flow: column;
  font-size: var(--font-size-regular);
  letter-spacing: 0;
  border-top: 1px solid var(--color-grey-medium);
  padding: var(--s6);
}
.report-index .report-title {
  color: inherit;
  font-size: calc(var(--s4) + var(--s2px));
  padding: 0 0 0 var(--s6);
  margin: 0 0 var(--s6) 0;
  line-height: var(--lineheight-regular);
  font-weight: var(--font-weight-bold);
}
.report-index .report-nav-section {
  background-color: var(--color-report-menu-background);
  box-shadow: 0 2px 0 0 var(--color-black);
  margin: var(--s2px) 0 0 0;
  color: inherit;
  padding: calc(var(--s5) - var(--s2px)) var(--s6);
  line-height: var(--lineheight-regular);
  font-size: var(--font-size-regular);
  display: flex;
  flex-direction: column;
}
.report-index .report-nav-section .section-title {
  display: flex;
  color: inherit;
  cursor: pointer;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  font-weight: var(--font-weight-medium);
}
.report-index .report-nav-section .section-title.expanded {
  margin: 0 0 calc(var(--s4) + var(--s2px)) 0;
}
.report-index .report-nav-section .section-title:active, .report-index .report-nav-section .section-title:hover, .report-index .report-nav-section .section-title:visited:hover {
  text-decoration: none;
}
.report-index .report-nav-section .section-title span:active, .report-index .report-nav-section .section-title span:hover {
  text-decoration: underline;
}
.report-index .report-nav-section .section-title svg {
  width: var(--s6);
  height: var(--s6);
  padding: var(--s3px);
}
.report-index .report-nav-section .section-arrow {
  fill: var(--color-orange-light);
}
.report-index .report-nav-section .navigation-item {
  font-size: var(--font-size-s);
  font-weight: var(--font-weight-medium);
  line-height: var(--lineheight-s);
  margin: 0 0 var(--s3) 0;
  display: flex;
  cursor: pointer;
  color: inherit;
}
.report-index .report-nav-section .navigation-item:last-of-type {
  margin: 0;
}
.report-index .report-nav-section .navigation-item:hover {
  text-decoration: underline;
}
.report-index .report-nav-section .navigation-item.font-strike {
  color: var(--color-disabled);
}
.report-index .report-nav-section .no-results-label {
  font-size: var(--font-size-s);
  font-weight: var(--font-weight-medium);
  line-height: var(--lineheight-s);
  margin: 0 0 var(--s3) 0;
  display: flex;
}
.report-index .report-nav-section .navigation-line-break {
  margin-bottom: var(--s3);
  margin-top: var(--s3);
  border-bottom: 1px solid var(--color-medium-grey);
}
.report-index .report-nav-section .key-focus-navigation {
  font-size: var(--font-size-s);
  padding-bottom: var(--s1);
  padding-top: var(--s1);
  cursor: pointer;
  color: inherit;
}
.report-index .report-nav-section .key-focus-navigation:hover {
  text-decoration: underline;
}
.report-index .report-nav-section .key-focus-navigation.font-strike {
  color: var(--color-disabled);
}
.report-index .report-nav-section .sub-navigation {
  font-size: var(--font-size-s);
  padding-left: var(--s3);
  color: inherit;
  cursor: pointer;
  display: flex;
}
.report-index .report-nav-section .sub-navigation.font-strike {
  color: var(--color-disabled);
}
.report-index .report-nav-section .sub-navigation:hover {
  text-decoration: underline;
}
