body {
  margin: 0;
  cursor: default !important;
}

.theme-light .notifications-landing-page {
  --color-grey-medium: var(--color-grey-darker);
  --color-grey-darker: var(--color-white);
}
.theme-light .notifications-landing-page .extended-tab-list .extended-tab-list-item {
  --tab-text-color: var(--color-orange-light-mode);
}

.notifications-landing-page {
  background-color: var(--color-page-background);
  display: flex;
  flex-direction: column;
  font-family: var(--font-family);
  line-height: var(--lineheight-regular);
  color: var(--project-color-main);
  max-width: 100vw;
  padding: var(--container-spacing);
}
.notifications-landing-page .heading-container .heading {
  font-family: var(--font-family);
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-bold);
  letter-spacing: 0;
  line-height: var(--lineheight-xl);
  color: inherit;
}
.notifications-landing-page .extended-tab-list {
  margin: 0px;
}
.notifications-landing-page .ui-checkbox {
  padding-top: 0px;
  padding-bottom: 0px;
}
