.progress-bar {
  align-items: center;
  display: flex;
  justify-content: space-between;
  max-width: 31.25rem;
}

.progress-info {
  color: var(--progress-bar-color);
  font-size: var(--font-size-s);
  margin-right: calc(var(--progress-bar-height) * 6);
  white-space: nowrap;
}

.progress-container {
  background: var(--progress-bar-track-background);
  border-radius: calc(var(--progress-bar-height) / 2);
  height: var(--progress-bar-height);
  overflow: hidden;
  position: relative;
  width: 100%;
}
.progress-container .progress-indicator {
  background: var(--color-green);
  border-radius: calc(var(--progress-bar-height) / 2);
  height: var(--progress-bar-height);
  position: absolute;
  transition: width var(--transition);
}
