.ui-tabs {
  max-width: 93vw;
}
@media screen and (min-width: 768px) {
  .ui-tabs {
    max-width: 95%;
  }
}
@media screen and (min-width: 1200px) {
  .ui-tabs {
    max-width: unset;
  }
}

.ui-tabs-background {
  scrollbar-color: var(--color-grey-variant) var(--scrollbar-track-color);
  scrollbar-width: thin;
  /* stylelint-disable-next-line scss/selector-no-redundant-nesting-selector */
  background-color: var(--background-secondary);
  overflow-y: auto;
}
.ui-tabs-background::-webkit-scrollbar {
  border-radius: 0;
  height: 0;
  width: 0;
}
.ui-tabs-background::-webkit-scrollbar-track {
  background: transparent;
}
.ui-tabs-background::-webkit-scrollbar-thumb {
  background: transparent;
  border-radius: 0;
}
.ui-tabs-background::-webkit-scrollbar-thumb:hover {
  background: transparent;
}
.ui-tabs-background {
  -ms-scrollbar-3dlight-color: transparent;
  -ms-scrollbar-face-color: transparent;
  scrollbar-face-color: transparent;
  -ms-scrollbar-track-color: transparent;
  scrollbar-track-color: transparent;
}

.ui-tab-list {
  align-items: flex-end;
  display: flex;
  height: var(--s14);
  list-style: none;
  margin: 0;
  padding: 0;
  width: min-content;
}
@media screen and (max-width: 991px) {
  .ui-tab-list {
    padding-right: 0.375rem;
  }
}
@media screen and (min-width: 768px) {
  .ui-tab-list {
    width: auto;
  }
}

.ui-tab-list-item {
  background-color: var(--tab-background-color);
  border: var(--s2px) solid var(--tab-background-color);
  border-radius: var(--s2px) var(--s2px) 0 0;
  color: var(--tab-text-color);
  cursor: pointer;
  font-size: var(--font-size-regular);
  line-height: var(--s11);
  margin-left: 0.375rem;
  padding: 0 var(--s6);
  white-space: nowrap;
}
.ui-tab-list-item:hover {
  text-decoration: underline;
}

.ui-tab-list-active {
  background-color: var(--tab-background-color-active);
  border-color: var(--tab-background-color-active);
  color: var(--tab-text-color-active);
}

.js-focus-visible .ui-tab-list-item.focus-visible:focus {
  border-color: var(--tab-border-keyboard-focus);
  border-bottom-color: var(--tab-background-color);
  border-width: var(--s2px);
  box-shadow: none;
  text-decoration: underline;
}
.js-focus-visible .ui-tab-list-active.focus-visible:focus {
  border-bottom-color: var(--tab-background-color-active);
  text-decoration: underline;
}

.ui-tab-list-inactive {
  color: var(--color-disabled);
  cursor: default;
}

.ui-tab-content {
  color: var(--project-color-main);
  margin-top: var(--s8);
}
