svg.icons path {
  fill: transparent;
  transition: all 0.2s ease-in-out;
}
svg.icons path.icon-white {
  fill: var(--icon-primary-color);
}
svg.icons.fill path.fill:not(.always-dark) {
  fill: var(--icon-primary-color);
  transition: fill var(--transition);
}
svg.icons.outline path.outline {
  fill: var(--icon-primary-color);
  transition: fill var(--transition);
}
svg.icons.icon-orange.fill path.fill {
  fill: var(--icon-secondary-color);
}
